import { Component, ReactNode } from "react";
import { RouteComponentProps } from "react-router-dom";
import style from './UnitPrice.module.css';
import { ResourceTypes, UnitPriceInfo, UnitPriceResourceInfo, UnitPriceResourceRequest, UnitPriceSpecificationInfo, UnitPriceSpecificationRequest } from "../../store/types/unit-price/UnitPriceInfo";
import Grid from "../common/grid/Grid";
import { UnitPriceExpencesInfo, UnitPriceSpecInfo, UnitPriceSummaryInfo, getResourceTypes, mapResourceInfoToUnitPriceSummaryInfo, mapUnitPriceSpecificationToUnitPriceSpecInfo } from "./UpgItemInfo";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettings } from "../../utils/helpers/settings-helper";
import { UnitPriceStatuses } from "../../store/types/unit-price/UnitPriceRequest";
import { StaticContext } from "react-router";

type Props = {
    item: UnitPriceInfo,
    specificationItems: UnitPriceSpecificationInfo[],
    resourceItems: UnitPriceResourceInfo[],
    getUnitPriceById: (id: number) => void,
    createUnitPriceResource: (unitPriceId: number, resource: UnitPriceResourceRequest) => void,
    deleteUnitPriceResource: (unitPriceId: number, id: number) => void,
    createUnitPriceSpecification: (unitPriceId: number, resource: UnitPriceSpecificationRequest) => void,
    deleteUnitPriceSpecification: (unitPriceId: number, id: number) => void
}

type RouteParams = {
    id: string,
    upgId: string
}

class UnitPriceCalculation extends Component<Props & RouteComponentProps<RouteParams>> {
    state: Readonly<{parentId: number, itemId: number, workerLaborCostPerHour: number, machineLaborCostPerHour: number}> = {
        parentId: Number.parseInt(this.props.match.params.id),
        itemId: Number.parseInt(this.props.match.params.upgId),
        workerLaborCostPerHour: this.props.resourceItems?.reduce((sum, p) => sum + p.workerLaborCostPerHour, 0) ?? 0,
        machineLaborCostPerHour: this.props.resourceItems?.reduce((sum, p) => sum + p.machineLaborCostPerHour, 0) ?? 0
    }

    indexes = ["А","Б","В","Г","Д","Е","Ё","Ж","З","И","Й","К","Л","М","Н","О","П","Р","С","Т","У","Ф","Х","Ц","Ч","Ш","Щ","Ю","Я"];

    componentDidMount(): void {
        if (this.state.parentId && this.state.itemId) {
            this.props.getUnitPriceById(this.state.itemId);
        }
    }
    
    componentDidUpdate(prevProps: Readonly<Props & RouteComponentProps<RouteParams, StaticContext, unknown>>, prevState: Readonly<{}>, snapshot?: any): void {
        // trigger update expences
    }

    getUnitPriceStatus = (type: UnitPriceStatuses) => {
        switch (type) {
            case UnitPriceStatuses.NoData:
                return "Нет данных";
            case UnitPriceStatuses.Calculation:
                return "Калькуляция";
            case UnitPriceStatuses.TemporaryRegulations:
                return "Временная норма";
            case UnitPriceStatuses.Canceled:
                return "Отменено";
            default:
                return "";
        }
    }

    getUnitPriceExpencesInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("code", buildGridItemSettings("Шифр ЕР", 160, ValueType.String));
        itemSettings.set("name", buildGridItemSettings("Наименование ЕР", 550, ValueType.String, true, true));
        itemSettings.set("unitId", buildGridItemSettings("Ед. изм.", 100, ValueType.String));
        itemSettings.set("workerLaborCostPerUnit", buildGridItemSettings("ТЗР, чел.-час.", 120, ValueType.Number));
        itemSettings.set("machineLaborCostPerUnit", buildGridItemSettings("ТЗМ, маш.-час.", 120, ValueType.Number));

        return buildGridSettings(itemSettings,  [], false, false, false, false, false);
    }

    getUnitPriceSummaryInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№пп", 160, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Наименование ресурса", 550, ValueType.String, true, true));
        itemSettings.set("type", buildGridItemSettingsWithPosibleValues("Тип ресурса", 100, ValueType.String, getResourceTypes()));
        itemSettings.set("workerLaborCostPerHour", buildGridItemSettings("ТЗР, чел.-час.", 120, ValueType.Number));
        itemSettings.set("machineLaborCostPerHour", buildGridItemSettings("ТЗМ, маш.-час.", 120, ValueType.Number));

        return buildGridSettings(itemSettings,  [], true, true, false, true, false);
    }

    getUnitPriceSpecInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("Код", 50, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Название", 300, ValueType.String, true, true));
        itemSettings.set("description", buildGridItemSettings("Описание", 700, ValueType.String, true, true));

        return buildGridSettings(itemSettings,  [], true, true, false, true, false);
    }

    createUnitPriceSpecification = (data: UnitPriceSpecInfo) => {
        this.props.createUnitPriceSpecification(this.state.itemId, {
            name: data.name, 
            description: data.description
        });
    }

    deleteUnitPriceSpecification = (id: number) => {
        this.props.deleteUnitPriceSpecification(this.state.itemId, id);
    }

    createUnitPriceResource = (data: UnitPriceSummaryInfo) => {
        let request: UnitPriceResourceRequest = {
            name: data.name, 
            type: data.type - 1,
            workerLaborCostPerHour: data.workerLaborCostPerHour,
            machineLaborCostPerHour: data.machineLaborCostPerHour
        };

        if (request.type === ResourceTypes.Worker) {
            request.machineLaborCostPerHour = 0;
            this.setState({workerLaborCostPerUnit: this.state.workerLaborCostPerHour + request.workerLaborCostPerHour});
        } else if (request.type === ResourceTypes.Machine) {
            request.workerLaborCostPerHour = 0;
            this.setState({machineLaborCostPerHour: this.state.machineLaborCostPerHour + request.machineLaborCostPerHour});
        }

        this.props.createUnitPriceResource(this.state.itemId, request);
    }

    deleteUnitPriceResource = (id: number) => {
        this.props.deleteUnitPriceResource(this.state.itemId, id);
    }

    render(): ReactNode {
        const items: UnitPriceExpencesInfo[] = [
            {
                code: this.props.item.code,
                name: this.props.item.name,
                unit: this.props.item.unit?.code,
                workerLaborCostPerUnit: this.state.workerLaborCostPerHour,
                machineLaborCostPerUnit: this.state.machineLaborCostPerHour
            }
        ];

        let resIndex = 1;
        let specIndex = 0;

        return (
            <div className={style.upgItemContainer}>
                <p><strong>Калькуляция затрат единичной расценки</strong></p>
                <div className={style.expences}>
                    <div className={style.status}>{this.getUnitPriceStatus(this.props.item.status)}</div>
                    <Grid 
                        items={items}
                        settings={this.getUnitPriceExpencesInfoSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
                <Grid 
                    items={this.props.resourceItems.map(item => mapResourceInfoToUnitPriceSummaryInfo(item, resIndex++))}
                    settings={this.getUnitPriceSummaryInfoSettings()}
                    getItems={() => {}}
                    createItem={this.createUnitPriceResource}
                    editItem={() => {}}
                    deleteItem={this.deleteUnitPriceResource}
                />
                <div style={{marginBlock: 10}}></div>
                <Grid 
                    items={this.props.specificationItems.map(item => mapUnitPriceSpecificationToUnitPriceSpecInfo(item, this.indexes[specIndex++]))}
                    settings={this.getUnitPriceSpecInfoSettings()}
                    getItems={() => {}}
                    createItem={this.createUnitPriceSpecification}
                    editItem={() => {}}
                    deleteItem={this.deleteUnitPriceSpecification}
                />
            </div>
        )
    }
}

export default UnitPriceCalculation;