import { Component, ReactNode } from "react";
import style from './Grid.module.css';
import { GridItemSettings } from "./GridSettings";
import { getDataSettings, getErrorSettings } from "../../../utils/helpers/settings-helper";

import React from "react";
import GridItemField from "./GridItemField";

type Props = {
    settings: Map<string, GridItemSettings>,
    itemMap: Map<string, any>,
    onValueChange: (item: Map<string, any>) => void
}

class AddNewGridItem extends Component<Props> {
    onValueChange = (name: string, value: any) => {
        let itemMap = this.props.itemMap;
        itemMap.set(name, value);
        this.props.onValueChange(itemMap);
    }

    render(): ReactNode  {
        return (
            <div className={style.item}>
                {
                    getDataSettings(this.props.settings).map(settings => 
                        <GridItemField
                            key={settings.name}
                            name={settings.name}
                            value={this.props.itemMap.get(settings.name)}
                            settings={this.props.settings.get(settings.name) ?? getErrorSettings()}
                            onValueChange={this.onValueChange}
                            isAddMode={true}
                            isSelected={false}
                            isEditMode={false}
                            isFocused={false}
                        />
                    )
                }
            </div>
        );
    }
}

export default AddNewGridItem;