import { Component, ReactNode } from "react";
import style from "./UnitPrice.module.css";
import { RegionInfo } from "../region/RegionInfo";
import { HiCheck, HiXMark } from "react-icons/hi2";

type Props = {
    regions: Array<RegionInfo>,
    onSubmit: (name: string, regionId: number, file: File) => void,
    onCancel: () => void
}

class CreateNewGuide extends Component<Props> {
    state: Readonly<{newUpgName: string, regionId: number, selectedFile: File | null}> = {
        newUpgName: "",
        regionId: 0,
        selectedFile: null
    }

    onNewUpgNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            newUpgName: e.currentTarget.value
        });
    }

    onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (!event.target.files)
            return;

        const file = event.target.files[0];
        this.setState({
            selectedFile: file
        })
    }

    onSubmit = () => {
        if (!this.state.selectedFile || !this.state.newUpgName || !this.state.regionId)
            return;

        this.props.onSubmit(this.state.newUpgName, this.state.regionId, this.state.selectedFile);

        this.setState({
            selectedFile: null,
            needToRedirect: true
        })
    }

    onSelectValueChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        this.setState({regionId: Number.parseInt(e.currentTarget.value)});
    }

    render(): ReactNode {
        return (
            <div className={style.newUpg}>
                <p><strong>Выберите нужный справочник</strong></p>
                <input type={"text"} placeholder={"Название справочника"} value={this.state.newUpgName} onChange={this.onNewUpgNameChange} />
                <select onChange={this.onSelectValueChange} value={this.state.regionId}>
                    {
                        !this.state.regionId &&
                            <option key={0} value={0}>-</option>
                    }
                    {
                        this.props.regions.map(region => 
                            <option key={region.id} value={region.id}>{region.name}</option>
                        )
                    }
                </select>
                <input type={"file"} id={"file"} onChange={this.onChange} />
                <label htmlFor={"file"} className={style.fileLabel}>{"Выберите файл"}</label>
                <div className={style.controlPanel}>
                    <HiCheck title={"Сохранить"} className={style.icon} onClick={this.onSubmit} />
                    <HiXMark title={"Отменить"} className={style.icon} onClick={this.props.onCancel} />
                </div>
            </div>
        )
    }
} 

export default CreateNewGuide;