import { Component, ReactNode } from "react";
import style from './Region.module.css';
import { RegionInfo } from "./RegionInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    items: Array<RegionInfo>,
    getRegions: (skip: number, take: number) => void,
    createRegion: (item: RegionInfo) => void,
    editRegion: (id: number, item: RegionInfo) => void,
    deleteRegion: (id: number) => void
}

class Region extends Component<Props> {
    getSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 100, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Регион", 350, ValueType.String, true, true));
        itemSettings.set("code", buildGridItemSettings("Код", 150, ValueType.String));

        return buildGridSettings(itemSettings);
    }

    render(): ReactNode  {
        return (
            <div className={style.container}>
                <Grid
                    items={this.props.items}
                    settings={this.getSettings()}
                    getItems={this.props.getRegions}
                    createItem={this.props.createRegion}
                    editItem={this.props.editRegion}
                    deleteItem={this.props.deleteRegion}
                />
            </div>
        );
    }
}

export default Region;