export interface ProjectWorkerInfo {
    id: number,
    index: number,
    type: string,
    name: string,
    costsPerHour: number,
    directPayment: number,
    personalIncomeTax: number,
    insurancePremiums: number,
    vacationAllowance: number,
    specialConditions: number,
    obtainingWorkPermitsCosts: number,
    consumablesCost: number,
    qualificationCosts: number
}

export const mapProjectWorkerInfoToGrid = (item: ProjectWorkerInfo, index: number) => ({
    id: item.id,
    index: index,
    type: item.type,
    name: item.name,
    costsPerHour: item.costsPerHour,
    directPayment: item.directPayment,
    personalIncomeTax: item.personalIncomeTax,
    insurancePremiums: item.insurancePremiums,
    vacationAllowance: item.vacationAllowance,
    specialConditions: item.specialConditions,
    obtainingWorkPermitsCosts: item.obtainingWorkPermitsCosts,
    consumablesCost: item.consumablesCost,
    qualificationCosts: item.qualificationCosts
});