import { Component, ReactNode } from "react";
import style from "./Summary.module.css";
import { RouteComponentProps } from "react-router-dom";
import Grid from "../common/grid/Grid";
import { Costs, PercentageCosts, SummaryInfo, mapSummaryToBaseSummary, mapSummaryToSummaryInfoCosts, mapSummaryToSummaryInfoCostsPerUnit } from "./SummaryInfo";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings, buildGridSettingsWithCrossTable } from "../../utils/helpers/settings-helper";

type Props = {
    summary: Array<SummaryInfo>,
    dirrectCosts: Array<Costs>,
    indirrectCosts: Array<PercentageCosts>,
    risks: Array<PercentageCosts>,
    profit: Array<PercentageCosts>
}

type RouteParams = {
    id: string
}

class Summary extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{projectId: number, selectedId: number}> = {
        projectId: Number(this.props.match.params.id),
        selectedId: 0
    }

    onItemSelected = (id: number): void => {
        this.setState({selectedId: id});
    }

    getProjectBaseSummarySettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("ubrCode", buildGridItemSettings("Код УБР", 70, ValueType.String, false));
        itemSettings.set("name", buildGridItemSettings("Наименование УБР", 350, ValueType.String, false, true));
        itemSettings.set("unit", buildGridItemSettings("Ед. измерения", 130, ValueType.String, false));
        itemSettings.set("quantity", buildGridItemSettings("Количество", 110, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    getProjectSummaryCostsSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("total", buildGridItemSettings("Cтоимость", 110, ValueType.Money, false));
        itemSettings.set("smr", buildGridItemSettings("Итого СМР", 110, ValueType.Money, false));
        itemSettings.set("dc", buildGridItemSettings("ПЗ", 110, ValueType.Money, false));
        itemSettings.set("ic", buildGridItemSettings("КЗ", 110, ValueType.Money, false));
        itemSettings.set("risks", buildGridItemSettings("Риски", 110, ValueType.Money, false));
        itemSettings.set("profit", buildGridItemSettings("Прибыль", 110, ValueType.Money, false));
        itemSettings.set("mtr", buildGridItemSettings("Итого МТР", 110, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    getProjectSummaryCostsPerUnitSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("ubrPerUnit", buildGridItemSettings("УБР за ед.", 110, ValueType.Money, false));
        itemSettings.set("smrPerUnit", buildGridItemSettings("СМР за ед.", 110, ValueType.Money, false));
        itemSettings.set("mtrPerUnit", buildGridItemSettings("МТР зе ед.", 110, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    getProjectCostsSettings = (indexHeader: string, nameHeader: string, includePersents: boolean): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("index", buildGridItemSettings(indexHeader, 70, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings(nameHeader, 350, ValueType.String, false, true));
        itemSettings.set("unit", buildGridItemSettings("Ед. измерения", 130, ValueType.String, false));
        itemSettings.set("quantity", buildGridItemSettings("Количество", 110, ValueType.Money, false));

        if (includePersents) {
            itemSettings.set("persents", buildGridItemSettings("%", 70, ValueType.Persents, false));
        }

        return buildGridSettings(itemSettings, [], false, false, false, true, false);
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                <p className={style.title}><strong>Общая сводка затрат</strong></p>
                <div className={style.gridHorizontalContainer}>
                    <Grid
                        items={this.props.summary.map(item => mapSummaryToBaseSummary(item))}
                        settings={this.getProjectBaseSummarySettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.summary.map(item => mapSummaryToSummaryInfoCosts(item))}
                        settings={this.getProjectSummaryCostsSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.summary.map(item => mapSummaryToSummaryInfoCostsPerUnit(item))}
                        settings={this.getProjectSummaryCostsPerUnitSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
                <div className={style.gridVerticalContainer}>
                    <p className={style.title}><strong>Анализ основных параметров</strong></p>
                    <div className={style.gridWrapper}>
                        <Grid
                            items={this.props.dirrectCosts}
                            settings={this.getProjectCostsSettings("1.", "Прямые затраты", false)}
                            getItems={() => {}}
                            createItem={() => {}}
                            editItem={() => {}}
                            deleteItem={() => {}}
                        />
                    </div>
                    <Grid
                        items={this.props.indirrectCosts}
                        settings={this.getProjectCostsSettings("2.", "Косвенные затраты", true)}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.risks}
                        settings={this.getProjectCostsSettings("3.", "Риски", true)}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.profit}
                        settings={this.getProjectCostsSettings("4.", "Прибыль", true)}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
            </div>
        );
    }
}

export default Summary;