import { Component, ReactNode } from "react";
import style from "./UnitPrice.module.css";
import { Redirect, RouteComponentProps } from "react-router-dom";
import { UpgItemInfo, mapUnitPriceToBaseUnitPriceInfo, mapUnitPriceToUnitPriceEnlargementInfo, mapUnitPriceToUnitPriceValuesInfo } from "./UpgItemInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { ArrayToMap, buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettings, buildGridSettingsWithCrossTable } from "../../utils/helpers/settings-helper";
import { UnitPriceInfo } from "../../store/types/unit-price/UnitPriceInfo";
import { MeasurementUnitInfo } from "../unit/MeasurementUnitInfo";
import { HiOutlineArrowLeftOnRectangle } from "react-icons/hi2";

type Props = {
    isProjectUpg: boolean,
    projectId: number | undefined,
    items: Array<UnitPriceInfo>,
    units: Array<MeasurementUnitInfo>,
    getUnitPrices: (skip: number, take: number) => void,
    editUnitPrice: (id: number, unitPrice: UnitPriceInfo) => void,
    getMeasurementUnits: (skip: number, take: number) => void
}

type RouteParams = {
    id: string
}

class UnitPrice extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{upgId: number, isRedirectingToCalculations: boolean, selectedId: number}> = {
        upgId: this.props.isProjectUpg ? 0 : Number(this.props.match.params.id),
        isRedirectingToCalculations: false,
        selectedId: 0
    }

    componentDidMount(): void {
        this.props.getUnitPrices(0, 1000); // TODO: get by proj id or upg list id
        this.props.getMeasurementUnits(0, 100);
    }
    
    onItemSelected = (id: number): void => {
        this.setState({selectedId: id});
    }

    onProjectEntering = (): void => {
        if (this.state.selectedId) {
            this.setState({isRedirectingToCalculations: true});
        }
    }

    getBaseUnitPriceInfoSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "Перейти к калькуляциям",
                iconComponent: HiOutlineArrowLeftOnRectangle,
                onClick: this.onProjectEntering
            }
        ];
        
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№пп", 70, ValueType.Number, false));
        itemSettings.set("groupCode", buildGridItemSettings("Группа ЕР", 90, ValueType.String, false));
        itemSettings.set("discipline", buildGridItemSettings("Дисциплина", 150, ValueType.String, false, true));
        itemSettings.set("code", buildGridItemSettings("Шифр ЕР", 125, ValueType.String, false, true));
        itemSettings.set("name", buildGridItemSettings("Наименование ЕР", 500, ValueType.String, true, true));
        itemSettings.set("unitId", buildGridItemSettingsWithPosibleValues("Ед. изм.", 80, ValueType.Number, ArrayToMap(this.props.units, p => p.id, p => p.code)));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, injectedControls, false, false, true, true, true);
    }

    getUnitPriceValuesInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("workerLaborCostPerUnit", buildGridItemSettings("Т/З ОР за ед.изм.", 150, ValueType.Money));
        itemSettings.set("machineLaborCostPerUnit", buildGridItemSettings("Т/З МиМ за ед.изм.", 150, ValueType.Money));

        if (this.props.isProjectUpg) {
            itemSettings.set("workerPaymentPerHour", buildGridItemSettings("Стоимость часа ОР", 170, ValueType.Money));
            itemSettings.set("workerPaymentPerUnit", buildGridItemSettings("ОЗП за ед.изм.", 150, ValueType.Money));
            itemSettings.set("machineOperationCostPerHour", buildGridItemSettings("Стоимость часа МиМ", 190, ValueType.Money));
            itemSettings.set("machineOperationCostPerUnit", buildGridItemSettings("ЭММ за ед.изм.", 150, ValueType.Money));
            itemSettings.set("directCostPerUnit", buildGridItemSettings("ПЗ за ед.изм.", 150, ValueType.Money));
        }

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, true, true, false);
    }

    getUnitPriceEnlargementInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("enlargement1Code", buildGridItemSettings("Код УП", 90, ValueType.String));
        itemSettings.set("enlargement1Name", buildGridItemSettings("Наименование УП", 190, ValueType.String, true, true));
        itemSettings.set("enlargement1UnitId", buildGridItemSettingsWithPosibleValues("Ед. изм.", 80, ValueType.Number, ArrayToMap(this.props.units, p => p.id, p => p.code)));
        itemSettings.set("enlargement1Coeficient", buildGridItemSettings("K", 80, ValueType.Money));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, true, true, false);
    }

    render(): ReactNode {
        let index = 1;

        return (
            <div className={style.unitPriceContainer}>
                <div className={style.gridContainer}>
                    <Grid 
                        items={this.props.items.map(item => mapUnitPriceToBaseUnitPriceInfo(item, index++))}
                        settings={this.getBaseUnitPriceInfoSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid 
                        items={this.props.items.map(item => mapUnitPriceToUnitPriceValuesInfo(item))}
                        settings={this.getUnitPriceValuesInfoSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid 
                        items={this.props.items.map(item => mapUnitPriceToUnitPriceEnlargementInfo(item))}
                        settings={this.getUnitPriceEnlargementInfoSettings()}
                        getItems={() => {}}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
                {
                    this.state.isRedirectingToCalculations && this.state.selectedId && this.props.isProjectUpg &&
                        <Redirect to={`/projects/${this.props.projectId}/projectUPG/${this.state.selectedId}`} />
                }
                {
                    this.state.isRedirectingToCalculations && this.state.selectedId && !this.props.isProjectUpg &&
                        <Redirect to={`/upg/${this.state.upgId}/${this.state.selectedId}`} />
                }
            </div>
        );
    }
}

export default UnitPrice;