import { Component, ReactNode } from "react";
import style from "./IndirectExpenses.module.css";
import { RouteComponentProps } from "react-router-dom";
import { IndirectExpensesInfo, mapIndirectExpensesToGridModel } from "./IndirectExpensesInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    items: Array<IndirectExpensesInfo>
}

type RouteParams = {
    id: string
}

class IndirectExpenses extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{projectId: number, total: number}> = {
        projectId: Number(this.props.match.params.id),
        total: this.props.items.reduce((sum, item) => sum + item.costs, 0)
    }

    getIndirectExpensesSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№ п/п", 70, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Наименование затрат", 500, ValueType.String));
        itemSettings.set("currency", buildGridItemSettings("Валюта", 90, ValueType.String));
        itemSettings.set("costs", buildGridItemSettings("Стоимость, руб.", 150, ValueType.String));

        return buildGridSettings(itemSettings);
    }

    render(): ReactNode {
        let index = 1;

        return (
            <div className={style.container}>
                <div className={style.total}>
                    <p><strong>{this.state.total.toFixed(2)}</strong></p>
                </div>
                <Grid 
                    items={this.props.items.map(item => mapIndirectExpensesToGridModel(item, index++))}
                    settings={this.getIndirectExpensesSettings()}
                    getItems={() => {}} // TODO: add all actions
                    createItem={() => {}}
                    editItem={() => {}}
                    deleteItem={() => {}}
                />
            </div>
        );
    }
}

export default IndirectExpenses;