import { Component, ReactNode } from "react";
import style from "./UnitPrice.module.css";
import { Redirect } from "react-router-dom";
import { UpgItemInfo } from "./UpgItemInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { ArrayToMap, buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettingsWithCrossTable } from "../../utils/helpers/settings-helper";
import { RegionInfo } from "../region/RegionInfo";
import { HiOutlineArrowLeftOnRectangle, HiPlus } from "react-icons/hi2";
import CreateNewGuide from "./CreateNewGuide";

type Props = {
    items: Array<UpgItemInfo>
    regions: Array<RegionInfo>,
    getRegions: (skip: number, take: number) => void,    
    uploadUpgFile: (name: string, regionId: number, file: File) => void
}

class UpgList extends Component<Props> { 
    state: Readonly<{isAddingNewGuide: boolean, isRedirectingToGuide: boolean, selectedId: number}> = {
        isAddingNewGuide: false,
        isRedirectingToGuide: false,
        selectedId: 0,
    }

    componentDidMount(): void {
        this.props.getRegions(0, 100);
    }

    onGuideEntering = (): void => {
        if (this.state.selectedId) {
            this.setState({isRedirectingToGuide: true});
        }
    }    
    
    onItemSelected = (id: number): void => {
        this.setState({selectedId: id});
    }

    onNewUpgCreating = (): void => {
        this.setState({isAddingNewGuide: true});
    }

    createGuide = (name: string, regionId: number, file: File) => {
        this.props.uploadUpgFile(name, regionId, file);
        this.setState({isAddingNewGuide: false});
    }

    onNewUpgCreationCancel = () => {
        this.setState({isAddingNewGuide: false});
    }

    getUpgListSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "Добавить справочник",
                iconComponent: HiPlus,
                onClick: this.onNewUpgCreating
            },
            {
                title: "Перейти к справочнику",
                iconComponent: HiOutlineArrowLeftOnRectangle,
                onClick: this.onGuideEntering
            },
        ];
        
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№", 70, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Название справочника", 500, ValueType.String, true, true));
        itemSettings.set("unitPricesCount", buildGridItemSettings("Кол-во расценок", 150, ValueType.Number, false));
        itemSettings.set("regionId", buildGridItemSettingsWithPosibleValues("Регион", 200, ValueType.Number, ArrayToMap(this.props.regions, p => p.id, p => p.name)));
        itemSettings.set("createdAt", buildGridItemSettings("Дата создания", 150, ValueType.String, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, injectedControls, false);
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    !this.state.isAddingNewGuide && 
                        <Grid 
                            items={this.props.items}
                            settings={this.getUpgListSettings()}
                            getItems={() => {}} // TODO: add all actions
                            createItem={() => {}}
                            editItem={() => {}}
                            deleteItem={() => {}}
                        />
                }
                {
                    this.state.isAddingNewGuide && 
                        <CreateNewGuide
                            regions={this.props.regions}
                            onSubmit={this.createGuide}
                            onCancel={this.onNewUpgCreationCancel}
                        />
                }
                {
                    this.state.isRedirectingToGuide && this.state.selectedId && 
                        <Redirect to={`/upg/${this.state.selectedId}`} />
                }
            </div>
        );
    }
}

export default UpgList;