import { Component, ReactNode } from "react";
import style from './Grid.module.css';
import { GridSettings } from "./GridSettings";
import { getDataSettings, getInitialVaulesMap, mapToObject } from "../../../utils/helpers/settings-helper";
import { HiOutlineTrash, HiPlus, HiArrowPath, HiXMark, HiCheck } from "react-icons/hi2";
import { Dictionary } from "@reduxjs/toolkit";
import GridItem from "./GridItem";
import AddNewGridItem from "./AddNewGridItem";

type Props<T> = {
    items: Array<T>,
    settings: GridSettings,
    getItems: (skip: number, take: number) => void,
    createItem: (item: T) => void,
    editItem: (id: number, item: T) => void,
    deleteItem: (id: number) => void
}

class Grid<T extends Dictionary<any>> extends Component<Props<T>> {
    state: Readonly<{selectedItemId: number, isAddMode: boolean, newItemMap: Map<string, any>}> = {
        selectedItemId: 0,
        isAddMode: false,
        newItemMap: getInitialVaulesMap(this.props.settings.dataSettings)
    }

    componentDidMount(): void {
        this.refreshData();
    }

    shouldDisplayControls = (): boolean => {
        return this.props.settings.canAddNewItems || this.props.settings.isRefreshable || this.props.settings.canDeleteItems;
    }

    refreshData = (): void => {
        if (this.props.settings.isRefreshable) {
            this.props.getItems(0, 100);
        }
    }

    activateAddMode = (): void => {
        this.setState({isAddMode: true});
    }

    deactivateAddMode = (): void => {
        this.setState({newItemMap: getInitialVaulesMap(this.props.settings.dataSettings)});
        this.setState({isAddMode: false});
    }

    saveNewItem = (): void => {
        const newItem = mapToObject<T>(this.state.newItemMap);
        this.props.createItem(newItem);

        this.setState({newItemMap: getInitialVaulesMap(this.props.settings.dataSettings)});
        this.deactivateAddMode();
    }

    deleteSelectedItem = (): void => {        
        if (this.props.settings.selectedItemId && this.props.settings.onSelected) {
            this.props.deleteItem(this.props.settings.selectedItemId);
            this.props.settings.onSelected(0);
        } else if (this.state.selectedItemId) {
            this.props.deleteItem(this.state.selectedItemId);
            this.setState({selectedItemId: 0});
        }
    }

    onNewItemEdit = (itemMap: Map<string, any>): void => {
        this.setState({itemMap: itemMap})
    }

    onItemSelect = (id: number): void => {
        if (this.props.settings.selectedItemId !== undefined && this.props.settings.onSelected) {
            this.props.settings.onSelected(id);
        } else {
            this.setState({selectedItemId: id});
        }
    }

    onEditedItemSave = (id: number, itemMap: Map<string, any>): void => {
        const item = mapToObject<T>(itemMap);
        this.props.editItem(id, item);
    }

    render(): ReactNode  {
        let gridItemKey = 1;
        let gridItemId = 1;
        let controlIndex = 1;

        const shouldDisplayControls = this.shouldDisplayControls();

        return (
            <div className={style.container}>
                {
                    shouldDisplayControls &&
                        <div className={style.controlPanel}>
                            {
                                this.props.settings.canAddNewItems && !this.state.isAddMode &&
                                    <HiPlus title={"Добавить"} className={style.icon} onClick={this.activateAddMode}/>
                            }
                            {
                                this.props.settings.canAddNewItems && this.state.isAddMode &&
                                    <HiXMark title={"Отменить"} className={style.icon}  onClick={this.deactivateAddMode}/>
                            }
                            {
                                this.props.settings.canAddNewItems && this.state.isAddMode &&
                                    <HiCheck title={"Создать"} className={style.icon} onClick={this.saveNewItem}/>
                            }
                            {
                                this.props.settings.isRefreshable && 
                                    <HiArrowPath title={"Обновить"} className={style.icon} onClick={this.refreshData} />
                            }
                            {
                                this.props.settings.canDeleteItems && 
                                    <HiOutlineTrash title={"Удалить"} className={style.icon} onClick={this.deleteSelectedItem} />
                            }
                            {
                                this.props.settings.injectedControls.map(injectedControl => 
                                    <injectedControl.iconComponent key={controlIndex++}  title={injectedControl.title} className={style.icon} onClick={injectedControl.onClick} />
                                )
                            }
                        </div>
                }
                {
                    !shouldDisplayControls && 
                        <div className={style.controlPanel}></div>
                }

                <div className={style.header}>
                    {
                        getDataSettings(this.props.settings.dataSettings).map(settings => 
                            <div key={settings.name} className={style.item} style={{width: settings.width}}>
                                <p>{settings.headerName}</p>
                            </div>
                        )
                    }
                </div>
                <div className={style.data}>
                    {
                        this.state.isAddMode && 
                            <AddNewGridItem
                                settings={this.props.settings.dataSettings}
                                itemMap={this.state.newItemMap}
                                onValueChange={this.onNewItemEdit}
                            />
                    }
                    {
                        this.props.items.map(item => 
                            <GridItem 
                                key={!isNaN(item.id) ? Number.parseInt(item.id) : gridItemKey++}
                                id={!isNaN(item.id) ? Number.parseInt(item.id) : gridItemId++}
                                item={item}
                                settings={this.props.settings}
                                selectedItemId={this.props.settings.selectedItemId !== undefined ? this.props.settings.selectedItemId : this.state.selectedItemId}
                                onItemSelect={this.onItemSelect}
                                onEditedItemSave={this.onEditedItemSave}
                            />)
                    }
                    {
                        this.props.items.length === 0 &&
                            <GridItem 
                                key={0}
                                id={0}
                                item={{}}
                                settings={this.props.settings}
                                selectedItemId={-1}
                                onItemSelect={() => {}}
                                onEditedItemSave={() => {}}
                            />
                    }
                </div>
            </div>
        );
    }
}

export default Grid;
