export interface SummaryInfo {
    ubrCode: string,
    name: string,
    unit: string,
    quantity: number,

    total: number,
    smr: number,
    dc: number,
    ic: number,
    risks: number,
    profit: number,
    mtr: number,

    ubrPerUnit: number,
    smrPerUnit: number,
    mtrPerUnit: number
}

export interface BaseSummaryInfo {    
    ubrCode: string,
    name: string,
    unit: string,
    quantity: number,
}

export interface SummaryInfoCosts {
    total: number,
    smr: number,
    dc: number,
    ic: number,
    risks: number,
    profit: number,
    mtr: number,
}

export interface SummaryInfoCostsPerUnit {
    ubrPerUnit: number,
    smrPerUnit: number,
    mtrPerUnit: number
}

export interface Costs {
    index: number,
    name: string,
    unit: string,
    quantity: number    
}

export interface PercentageCosts extends Costs {
    persents: number
}

export const mapSummaryToBaseSummary = (item: SummaryInfo): BaseSummaryInfo => ({
    ubrCode: item.ubrCode,
    name: item.name,
    unit: item.unit,
    quantity: item.quantity,
});

export const mapSummaryToSummaryInfoCosts = (item: SummaryInfo): SummaryInfoCosts => ({
    total: item.total,
    smr: item.smr,
    dc: item.dc,
    ic: item.ic,
    risks: item.risks,
    profit: item.profit,
    mtr: item.mtr,
});

export const mapSummaryToSummaryInfoCostsPerUnit = (item: SummaryInfo): SummaryInfoCostsPerUnit => ({
    ubrPerUnit: item.ubrPerUnit,
    smrPerUnit: item.smrPerUnit,
    mtrPerUnit: item.mtrPerUnit
});