import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import { createWorker, deleteWorkerRegion, createWorkerRegion, editWorkerRegion, editWorker, getWorkers } from "../../store/actions/worker-actions";
import { WorkerTypes } from "../worker/WorkerInfo";
import Workers from "../worker/Workers";
import { getRegions } from "../../store/actions/region-actions";

const type = WorkerTypes.Machine;

const mapState = (store: AppStore) => {
    return ({
        workers: store.workersData.workers.filter(p => p.type === type),
        regions: store.regionsData.regions,
        type: type,
        titleName: "Наименование МиМ"
    });
}

const mapActions = {
    getWorkers: getWorkers,
    getRegions: getRegions,
    createWorker: createWorker,
    editWorker: editWorker,
    createWorkerRegion: createWorkerRegion,
    editWorkerRegion: editWorkerRegion,
    deleteWorkerRegion: deleteWorkerRegion
};

export default connect(mapState, mapActions)(Workers);