import { Dictionary, GridItemSettings, GridSettings, InjectedControl, KeyValuePair, ValueType } from "../../components/common/grid/GridSettings";

export const buildGridItemSettings = (headerName: string, width: number, valueType: ValueType, 
    isEditable: boolean = true, alignLeft: boolean = false, isVisible: boolean = true, defaultValueIfEmpty: any = "-"): GridItemSettings =>  ({
        isVisible: isVisible,
        isEditable: isEditable,
        alignLeft: alignLeft,
        headerName: headerName,
        valueType: valueType,
        posibleValues: new Map<any, any>(),
        width: width,
        defaultValueIfEmpty: defaultValueIfEmpty,
        name: "",
})

export const buildGridItemSettingsWithPosibleValues = (headerName: string, width: number, valueType: ValueType, posibleValues: Map<any, any> = new Map<any, any>(),
    isEditable: boolean = true, alignLeft: boolean = false, isVisible: boolean = true, defaultValueIfEmpty: string = "-"): GridItemSettings =>  ({
        isVisible: isVisible,
        isEditable: isEditable,
        alignLeft: alignLeft,
        headerName: headerName,
        valueType: valueType,
        posibleValues: posibleValues,
        width: width,
        defaultValueIfEmpty: defaultValueIfEmpty,
        name: "",
})

export const buildGridSettings = (dataSettings: Map<string, GridItemSettings>, injectedControls: Array<InjectedControl> = [], canAddNewItems: boolean = true, 
    canDeleteItems: boolean = true, isItemsEditable: boolean = true, isItemsSelectable: boolean = true, isRefreshable: boolean = true) : GridSettings => ({
        dataSettings: dataSettings,
        injectedControls: injectedControls,
        selectedItemId: undefined,
        onSelected: undefined,
        isItemsSelectable: isItemsSelectable,
        isItemsEditable: isItemsEditable,
        isRefreshable: isRefreshable,
        canDeleteItems: canDeleteItems,
        canAddNewItems: canAddNewItems
})

export const buildGridSettingsWithCrossTable = (dataSettings: Map<string, GridItemSettings>, selectedItemId: number, onSelected: ((id: number) => void),
    injectedControls: Array<InjectedControl> = [], canAddNewItems: boolean = true, canDeleteItems: boolean = true, 
    isItemsEditable: boolean = true, isItemsSelectable: boolean = true, isRefreshable: boolean = true) : GridSettings => ({
        dataSettings: dataSettings,
        injectedControls: injectedControls,
        selectedItemId: selectedItemId,
        onSelected: onSelected,
        isItemsSelectable: isItemsSelectable,
        isItemsEditable: isItemsEditable,
        isRefreshable: isRefreshable,
        canDeleteItems: canDeleteItems,
        canAddNewItems: canAddNewItems
})

export const getDataSettings = (settings: Map<string, GridItemSettings>) : GridItemSettings[] => {
    let dataSettings = new Array<GridItemSettings>(settings.size);

    let i = 0;
    settings.forEach((settings, name) => {
        settings.name = name;
        dataSettings[i++] = settings;
    });

    return [...dataSettings.filter(settings => settings.isVisible)];
}

export const mapToArray = (map: Map<any, any>) : KeyValuePair[] => {
    let array = new Array<KeyValuePair>(map.size);

    let i = 0;
    map.forEach((value, key) => {
        array[i++] = {key, value};
    });

    return array;
}

export const getErrorSettings = (): GridItemSettings => {
    console.log("Error: grid item settings are not provided");
    return buildGridItemSettings("error", 100, ValueType.String);    
}

export const getInitialVaulesMap = (settings: Map<string, GridItemSettings>): Map<string, any> => {
    let itemMap = new Map<string, any>();

    getDataSettings(settings).forEach(settings => {
        itemMap.set(settings.name, settings.isEditable ? getDefaultValue(settings.valueType) : null);
    });

    return itemMap;
}

export const getActualVaulesMap = <T extends Dictionary<any>>(settings: Map<string, GridItemSettings>, item: T): Map<string, any> => {
    let itemMap = new Map<string, any>();

    settings.forEach((_, name) => {
        itemMap.set(name, item[name]);
    });

    return itemMap;
}

export const getDefaultValue = (type: ValueType): boolean | number | string | null => {
    switch (type) {
        case ValueType.Money:
            return 0.00;

        case ValueType.Number:
            return 0;

        case ValueType.String:
            return "";

        default:
            return null;
    }
}

export const mapToObject = <T extends Dictionary<any>>(map: Map<string, any>) :T => (
    Array.from(map).reduce((obj, [key, value]) => (
        Object.assign(obj, { [key]: value })
      ), {} as T)
)

export const ArrayToMap = <T>(array: Array<T>, keySelector: (element: T) => any, valueSelector: (element: T) => any): Map<any, any> => {
    const map = new Map<any, any>();

    array.forEach(element => {
        map.set(keySelector(element), valueSelector(element));
    });

    return map;
}