import { IconType } from "react-icons"

export interface GridSettings {
    isItemsSelectable: boolean,
    isItemsEditable: boolean,
    isRefreshable: boolean,
    canDeleteItems: boolean,
    canAddNewItems: boolean,
    dataSettings: Map<string, GridItemSettings>,
    injectedControls: Array<InjectedControl>,
    selectedItemId: number | undefined,
    onSelected: ((id: number) => void) | undefined,
    // isAddMode: boolean | undefined,
    // onAddMode: () => void | undefined,
    // isEditMode: boolean | undefined, // only for case selecting active item while some table is in edit mode on it
}

export interface GridItemSettings {
    isVisible: boolean,
    isEditable: boolean,
    alignLeft: boolean,
    headerName: string,
    valueType: ValueType,
    posibleValues: Map<any, any>,
    width: number,
    defaultValueIfEmpty: string,
    name: string,
}

export interface InjectedControl {
    title: string,
    iconComponent: IconType,
    onClick: () => void
}

export enum ValueType {
    String,
    Number,
    Money,
    Persents
}

export interface IGridable extends Dictionary<any> {
}

export interface Dictionary<T> {
    [key: string] : T
}

export interface KeyValuePair {
    key: any,
    value: any
}