import { IGridable } from "../common/grid/GridSettings";

export interface ProjectInfo {
    id: number,
    name: string,
    regionId: number,
    description: string,
    createdDate: string,
    isSmtrSetuped: boolean, // rm
    isStructureSetuped: boolean, // rm
    isUpgSetuped: boolean, // rm
    startDate: string,
    endDate: string,
    category: ProjectCategories,
    status: ProjectStatuses,
    cost: number
}

export interface CreateProjectRequest {
    name: string,
    regionId: number,
    description: string,
}

export interface BaseProjectInfo extends IGridable {    
    id: number,
    name: string,
    regionId: number
}

export interface ProjectPeriodInfo extends IGridable {    
    id: number,
    period: string,
}

export interface ProjectStatusInfo extends IGridable {    
    id: number,
    status: ProjectStatuses,
}

export interface ProjectCostInfo extends IGridable {    
    id: number,
    cost: number,
}

export const mapProjectToProjectPeriod = (item: ProjectInfo): ProjectPeriodInfo => ({
    id: item.id,
    period: `${item.startDate} - ${item.endDate}`
});

export const mapProjectToProjectStatus = (item: ProjectInfo): ProjectStatusInfo => ({
    id: item.id,
    status: item.status
});

export const mapProjectToProjectCost = (item: ProjectInfo): ProjectCostInfo => ({
    id: item.id,
    cost: item.cost
});

export const mapProjectToBaseProject = (item: ProjectInfo, regions: Map<any, any>): BaseProjectInfo => ({
    id: item.id,
    name: item.name,
    regionId: item.regionId
});

export enum ProjectStatuses {
    None,
    OwnAssessment
}

export const getProjectStatuses = (): Map<any, any> => {
    const map = new Map<any, any>();
    map.set(0, "Нет статуса");
    map.set(1, "Собственная оценка");
    return map;
}

export enum ProjectCategories {
    New = 1,
    Started = 2,
    Paused = 4,
    Finished = 8,
    Current = New | Started,
    All = New | Started | Paused | Finished
}

export const getProjectCategories = (): Map<any, any> => {
    const map = new Map<any, any>();
    map.set(0, "Новый");
    map.set(1, "Текущий");
    map.set(2, "Остановленный");
    map.set(3, "Завершенный");
    return map;
}