export interface Theme {
    name: string,
    displayName: string,
    mainColor: string
    backColor: string,
    selectionColor: string,
    borderColor: string,
    inputColor: string
}

export const themes: Array<Theme> = [
    {
        name: "light",
        displayName: "Светлая тема",
        mainColor: "#3E5368",
        backColor: "#EFEFEC",
        selectionColor: "#B3A154",
        borderColor: "#222B35",
        inputColor: "#e8e0ca",
    },
    {
        name: "dark",
        displayName: "Темная тема",
        mainColor: "#d7d7e0",
        backColor: "#19141f",
        selectionColor: "#718cd1",
        borderColor: "#484d51",
        inputColor: "#8cb1e2",
    },
    {
        name: "blackWhite",
        displayName: "Контрастная тема",
        mainColor: "#19141f",
        backColor: "#e9e9ec",
        selectionColor: "#718cd1",
        borderColor: "#484d51",
        inputColor: "#8cb1e2",
    },
    // {
    //     name: "epileptic",
    //     displayName: "Эпилептическая тема",
    //     mainColor: "red",
    //     backColor: "black",
    //     selectionColor: "blue",
    //     borderColor: "white",
    //     inputColor: "#e8e0ca",
    // }
];

export const getActiveThemeName = (): string => {
    return localStorage.getItem("theme") ?? themes[0].name
}

export const setActiveTheme = (name: string) => {
    localStorage.setItem("theme", name);
}

export const activateTheme = (name: string = getActiveThemeName()) => {
    const theme = themes.find(p => p.name === name);
    if (!theme) {
        return;
    }

    document.documentElement.style.setProperty(`--main-color`, theme.mainColor);
    document.documentElement.style.setProperty(`--back-color`, theme.backColor);
    document.documentElement.style.setProperty(`--border-color`, theme.borderColor);
    document.documentElement.style.setProperty(`--selection-color`, theme.selectionColor);
    document.documentElement.style.setProperty(`--input-color`, theme.inputColor);
}

