export interface ProjectMachineInfo {
    id: number,
    index: number,
    type: string,
    name: string,
    costsPerHour: number,
    directPayment: number
}

export const mapProjectMachineInfoToGrid = (item: ProjectMachineInfo, index: number) => ({
    id: item.id,
    index: index,
    type: item.type,
    name: item.name,
    costsPerHour: item.costsPerHour,
    directPayment: item.directPayment
});