import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import { editProject, getProjectById } from "../../store/actions/project-actions";
import Project from "./Project";
import { withRouter } from "react-router-dom";
import { compose } from "redux";
import { getRegions } from "../../store/actions/region-actions";

const mapState = (store: AppStore) => {
    return ({
        item: store.projectsData.activeProject,
        regions: store.regionsData.regions
    });
}

export default compose<React.ComponentType>(
    connect(mapState, { getProjectById, editProject, getRegions }),
    withRouter
)(Project);
