import { ResourceTypes, UnitPriceInfo, UnitPriceResourceInfo, UnitPriceSpecificationInfo } from "../../store/types/unit-price/UnitPriceInfo"
import { WorkerTypes } from "../worker/WorkerInfo"

export interface UpgItemInfo {
    id: number,
    name: string,
    unitPricesCount: number,
    regionId: number,
    createdAt: string
}

export interface BaseUnitPriceInfo {
    id: number,
    index: number,
    groupCode: string,
    discipline: string,
    name: string,
    code: string,
    unitId: number
}

export interface UnitPriceValuesInfo {
    workerLaborCostPerUnit: number,
    machineLaborCostPerUnit: number,
    workerPaymentPerHour: number,
    workerPaymentPerUnit: number,
    machineOperationCostPerHour: number,
    machineOperationCostPerUnit: number,
    directCostPerUnit: number,
}

export interface UnitPriceEnlargementInfo {
    enlargement1UnitId: number,
    enlargement1Code: string,
    enlargement1Name: string,
    enlargement1Coeficient: number,
}

export interface UnitPriceExpencesInfo {
    code: string,
    name: string,
    unit: string,
    workerLaborCostPerUnit: number,
    machineLaborCostPerUnit: number
}

export interface UnitPriceSummaryInfo {
    id: number,
    index: number,
    name: string,
    type: ResourceTypes,
    workerLaborCostPerHour: number,
    machineLaborCostPerHour: number
}

export interface UnitPriceSpecInfo {
    id: number,
    index: string,
    name: string,
    description: string
}

export const getResourceTypes = (): Map<any, any> => {
    const map = new Map<any, any>();
    map.set(1, "МиМ");
    map.set(2, "ОР");
    return map;
}

export const mapResourceInfoToUnitPriceSummaryInfo = (item: UnitPriceResourceInfo, index: number): UnitPriceSummaryInfo => ({
    id: item.id,
    index: index,
    name: item.name,
    type: item.type + 1,    
    workerLaborCostPerHour: item.workerLaborCostPerHour,
    machineLaborCostPerHour: item.machineLaborCostPerHour
});

export const mapUnitPriceSpecificationToUnitPriceSpecInfo = (item: UnitPriceSpecificationInfo, index: string): UnitPriceSpecInfo => ({
    id: item.id,
    index: index,
    name: item.name,
    description: item.description
});

export const mapUnitPriceToBaseUnitPriceInfo = (item: UnitPriceInfo, index: number): BaseUnitPriceInfo => ({
    id: item.id,
    index: index,
    groupCode: item.groupCode,
    discipline: item.discipline,
    name: item.name,
    code: item.code,
    unitId: item.unit.id
});

export const mapUnitPriceToUnitPriceValuesInfo = (item: UnitPriceInfo): UnitPriceValuesInfo => ({
    workerLaborCostPerUnit: item.workerLaborCostPerUnit,
    machineLaborCostPerUnit: item.machineLaborCostPerUnit,
    workerPaymentPerHour: item.workerPaymentPerHour,
    workerPaymentPerUnit: item.workerPaymentPerUnit,
    machineOperationCostPerHour: item.machineOperationCostPerHour,
    machineOperationCostPerUnit: item.machineOperationCostPerUnit,
    directCostPerUnit: item.directCostPerUnit
});

export const mapUnitPriceToUnitPriceEnlargementInfo = (item: UnitPriceInfo): UnitPriceEnlargementInfo => ({
    enlargement1Code: item.enlargement1Code,
    enlargement1Name: item.enlargement1Name,
    enlargement1UnitId: item.unit.id,
    enlargement1Coeficient: item.enlargement1Coeficient,
});