import { Component, ReactNode } from "react";
import style from './Grid.module.css';
import { GridItemSettings, GridSettings } from "./GridSettings";
import { getActualVaulesMap, getDataSettings, getErrorSettings } from "../../../utils/helpers/settings-helper";
import { Dictionary } from "@reduxjs/toolkit";
import React from "react";
import GridItemField from "./GridItemField";

type Props<T> = {
    id: number,
    item: T,
    settings: GridSettings,
    selectedItemId: number,
    onItemSelect: (id: number) => void,
    onEditedItemSave: (id: number, itemMap: Map<string, any>) => void
}

class GridItem<T extends Dictionary<any>> extends Component<Props<T>> {
    state: Readonly<{itemRef: React.RefObject<HTMLDivElement> | undefined, itemMap: Map<string, any>, isEditMode: boolean, touched: boolean, focusedFieldIndex: number}> = {
        itemRef: undefined,
        itemMap: getActualVaulesMap(this.props.settings.dataSettings, this.props.item),
        isEditMode: false,
        touched: false,
        focusedFieldIndex: -1
    }

    handleClickOutside = (event: MouseEvent): void => {
        if (!this.state.itemRef?.current?.contains(event.target as Node)) {
           this.onSave();
        }
    }

    onValueChange = (name: string, value: any) => {
        let itemMap = this.state.itemMap;
        itemMap.set(name, isNaN(value) ? value : Number.parseFloat(value));
        this.setState({itemMap: itemMap, touched: true});
    }

    onClick = () => {
        if (this.state.isEditMode || !this.props.settings.isItemsSelectable) {
            return;
        }
        
        if (this.props.id !== this.props.selectedItemId) {
            this.props.onItemSelect(this.props.id);
        } else {
            this.props.onItemSelect(0);
        }
    }

    onDoubleClick = (e: React.MouseEvent<HTMLDivElement>) =>  {
        if (this.props.settings.isItemsEditable && !this.state.isEditMode) {
            this.setState({
                isEditMode: true, 
                itemRef: React.createRef<HTMLDivElement>(), 
                focusedFieldIndex: this.getFieldIndexToFocus(e)
            });

            document.addEventListener("mousedown", this.handleClickOutside);
        }
    }

    getFieldIndexToFocus = (e: React.MouseEvent<HTMLDivElement>): number => {
        for (let i = 0; i < e.currentTarget.children.length; i++) {
            if (e.currentTarget?.children[i] === (e.target as HTMLElement)?.parentElement 
                || e.currentTarget?.children[i] === (e.target as HTMLElement)?.parentElement?.parentElement) {
                return i;
            }
        }
        return -1;
    }

    onSave = () => {
        if (this.state.isEditMode) {
            if (this.state.touched) {
                this.props.onEditedItemSave(this.props.id, this.state.itemMap);
            }

            document.removeEventListener("mousedown", this.handleClickOutside);
            this.props.onItemSelect(this.props.id);
            this.setState({
                isEditMode: false, 
                touched: false, 
                itemRef: undefined,
                focusedFieldIndex: -1
            });
        }
    }

    render(): ReactNode  {
        let fieldIndex = 0;
        return (
            <div className={style.item} onClick={this.onClick} onDoubleClick={this.onDoubleClick} ref={this.state.itemRef}>
                {
                    getDataSettings(this.props.settings.dataSettings).map(settings => 
                        <GridItemField
                            key={settings.name}
                            name={settings.name}
                            value={this.state.itemMap.get(settings.name)}
                            settings={this.props.settings.dataSettings.get(settings.name) ?? getErrorSettings()}
                            isAddMode={false}
                            isEditMode={this.state.isEditMode}
                            isSelected={this.props.id === this.props.selectedItemId}
                            isFocused={this.state.focusedFieldIndex === fieldIndex++}
                            onValueChange={this.onValueChange}
                        />
                    )
                }
            </div>
        );
    }
}

export default GridItem;