import { Component, ReactNode } from "react";
import style from "./ProjectWorkers.module.css";
import { RouteComponentProps } from "react-router-dom";
import { ProjectWorkerInfo, mapProjectWorkerInfoToGrid } from "./ProjectWorkerInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    items: Array<ProjectWorkerInfo>
}

type RouteParams = {
    id: string
}

class ProjectWorkers extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{projectId: number}> = {
        projectId: Number(this.props.match.params.id),
    }

    getProjectWorkerInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№ п/п", 60, ValueType.Number, false));
        itemSettings.set("type", buildGridItemSettings("Тип", 80, ValueType.String));
        itemSettings.set("name", buildGridItemSettings("Наименование ресурса", 350, ValueType.String, true, true));
        itemSettings.set("costsPerHour", buildGridItemSettings("Стоимость часа", 150, ValueType.Money, false));
        itemSettings.set("directPayment", buildGridItemSettings("З/П на руки", 150, ValueType.Money));
        itemSettings.set("personalIncomeTax", buildGridItemSettings("НДФЛ", 150, ValueType.Money));
        itemSettings.set("insurancePremiums", buildGridItemSettings("Страховые взносы", 150, ValueType.Money));
        itemSettings.set("vacationAllowance", buildGridItemSettings("Резерв на оплату отпусков", 150, ValueType.Money));
        itemSettings.set("specialConditions", buildGridItemSettings("Особые условия производства", 150, ValueType.Money));
        itemSettings.set("obtainingWorkPermitsCosts", buildGridItemSettings("Расходы по оформлению", 150, ValueType.Money));
        itemSettings.set("consumablesCost", buildGridItemSettings("Расходы на инвентарь", 150, ValueType.Money));
        itemSettings.set("qualificationCosts", buildGridItemSettings("Сертификация, обучение", 150, ValueType.Money));

        return buildGridSettings(itemSettings);
    }

    render(): ReactNode {
        let index = 1;

        return (
            <div className={style.container}>
                <Grid
                    items={this.props.items.map(item => mapProjectWorkerInfoToGrid(item, index++))}
                    settings={this.getProjectWorkerInfoSettings()}
                    getItems={() => {}}
                    createItem={() => {}}
                    editItem={() => {}}
                    deleteItem={() => {}}
                />
            </div>
        );
    }
}

export default ProjectWorkers;