import { Component, ReactNode } from "react";
import style from "./Settings.module.css";
import { activateTheme, getActiveThemeName, setActiveTheme, themes } from "../../utils/helpers/theme-helper";


class Settings extends Component {
    state: Readonly<{currentTheme: string}> = {
        currentTheme: getActiveThemeName()
    }

    onThemeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({currentTheme: e.currentTarget.value});
        setActiveTheme(e.currentTarget.value);
        activateTheme(e.currentTarget.value);
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                <p><strong>Сменить цветовую тему:</strong></p>
                {
                    themes.map(theme => 
                        <label key={theme.name} >
                            <input 
                                type={"radio"} 
                                value={theme.name} 
                                checked={this.state.currentTheme == theme.name} 
                                onChange={this.onThemeChange} 
                            />
                            {theme.displayName}
                        </label>
                    )
                }
            </div>
        );
    }
}

export default Settings;
