import { Component, ReactNode } from "react";
import style from './Wor.module.css';
import { WorItemInfo, mapWorItemInfoToWorBaseInfo, mapWorItemInfoToWorPriceInfo, mapWorItemInfoToWorPriceTotal } from "./WorItemInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettings, buildGridSettingsWithCrossTable } from "../../utils/helpers/settings-helper";
import { RouteComponentProps } from "react-router-dom";
import { RouteParams } from "../../utils/helpers/params";

type Props = {
    items: Array<WorItemInfo>,
    getWor: (id: number) => void
}

class Wor extends Component<Props & RouteComponentProps<RouteParams>> {
    state: Readonly<{projectId: number, selectedId: number}> = {
        projectId: Number(this.props.match.params.id),
        selectedId: 0
    }

    onItemSelected = (id: number): void => {
        this.setState({selectedId: id});
    }

    getWor = (skip: number, take: number) => {
        this.props.getWor(this.state.projectId);
    }

    getWorBaseInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("index", buildGridItemSettings("№пп", 60, ValueType.Number, false));
        itemSettings.set("baseCode", buildGridItemSettings("Группа ЕР", 90, ValueType.String));
        itemSettings.set("subject", buildGridItemSettings("Дисциплина", 160, ValueType.String));
        itemSettings.set("code", buildGridItemSettings("Шифр ЕР", 120, ValueType.String));
        itemSettings.set("name", buildGridItemSettings("Наименование ЕР", 700, ValueType.String, false, true));
        itemSettings.set("units", buildGridItemSettings("Ед. изм.", 70, ValueType.String));
        itemSettings.set("volume", buildGridItemSettings("Объем работ", 130, ValueType.Money));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, true);
    }

    getWorPriceInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("workerLaborPerUnit", buildGridItemSettings("Т/З ОР / ед.", 120, ValueType.Money, false));
        itemSettings.set("machineLaborPerUnit", buildGridItemSettings("Т/З МиМ / ед.", 120, ValueType.Money, false));
        itemSettings.set("workerCostsPerHour", buildGridItemSettings("Ст. часа ОР", 120, ValueType.Money, false));
        itemSettings.set("workerCostsPerUnit", buildGridItemSettings("ОЗП / ед.", 120, ValueType.Money, false));
        itemSettings.set("machineCostsPerHour", buildGridItemSettings("Ст. часа МиМ", 120, ValueType.Money, false));
        itemSettings.set("machineCostsPerUnit", buildGridItemSettings("ЭММ / ед.", 120, ValueType.Money, false));
        itemSettings.set("dirrectCostsPerUnit", buildGridItemSettings("ПЗ / ед.", 120, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    getWorPriceTotalSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("workerLabor", buildGridItemSettings("Т/З ОР итого", 120, ValueType.Money, false));
        itemSettings.set("machineLabor", buildGridItemSettings("Т/З МиМ итого", 125, ValueType.Money, false));
        itemSettings.set("workerCosts", buildGridItemSettings("Итого ОЗП", 120, ValueType.Money, false));
        itemSettings.set("machineCosts", buildGridItemSettings("Итого ЭММ", 120, ValueType.Money, false));
        itemSettings.set("dirrectCosts", buildGridItemSettings("Итого ПЗ", 120, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    render(): ReactNode  {
        let index = 1;
        return (
            <div className={style.container}>
                <div className={style.gridContainer}>
                    <Grid
                        items={this.props.items.map(item => mapWorItemInfoToWorBaseInfo(item, index++))}
                        settings={this.getWorBaseInfoSettings()}
                        getItems={this.getWor}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.items.map(item => mapWorItemInfoToWorPriceInfo(item))}
                        settings={this.getWorPriceInfoSettings()}
                        getItems={this.getWor}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                    <Grid
                        items={this.props.items.map(item => mapWorItemInfoToWorPriceTotal(item))}
                        settings={this.getWorPriceTotalSettings()}
                        getItems={this.getWor}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
            </div>
        );
    }
}

export default Wor;