import { Component, ReactNode } from "react";
import style from "./ProjectMachines.module.css";
import { RouteComponentProps } from "react-router-dom";
import { ProjectMachineInfo, mapProjectMachineInfoToGrid } from "./ProjectMachineInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    items: Array<ProjectMachineInfo>
}

type RouteParams = {
    id: string
}

class ProjectMachines extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{projectId: number}> = {
        projectId: Number(this.props.match.params.id),
    }

    getProjectMachineInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№ п/п", 60, ValueType.Number, false));
        itemSettings.set("type", buildGridItemSettings("Тип", 80, ValueType.String));
        itemSettings.set("name", buildGridItemSettings("Наименование ресурса", 650, ValueType.String, true, true));
        itemSettings.set("costsPerHour", buildGridItemSettings("Стоимость часа", 200, ValueType.Money, false));
        itemSettings.set("directPayment", buildGridItemSettings("Стоимость часа за работу", 250, ValueType.Money));

        return buildGridSettings(itemSettings);
    }

    render(): ReactNode {
        let index = 1;

        return (
            <div className={style.container}>
                <Grid
                    items={this.props.items.map(item => mapProjectMachineInfoToGrid(item, index++))}
                    settings={this.getProjectMachineInfoSettings()}
                    getItems={() => {}}
                    createItem={() => {}}
                    editItem={() => {}}
                    deleteItem={() => {}}
                />
            </div>
        );
    }
}

export default ProjectMachines;