import { Component, ReactNode } from "react";
import style from './Worker.module.css';
import { CreateWorkerRegionRequest, CreateWorkerRequest, EditWorkerRegionRequest, EditWorkerRequest, WorkerInfo, WorkerInfoWithRegionPrices, WorkerTypes, getWorkerTypes, mapWorkerInfoToGridItem } from "./WorkerInfo";
import { RegionInfo } from "../region/RegionInfo";
import { HiOutlineGlobeAlt, HiOutlineX, HiPlus } from 'react-icons/hi';
import Worker from "./Worker";
import AddNewWorker from "./AddNewWorker";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettings } from "../../utils/helpers/settings-helper";
import { Redirect } from "react-router-dom";

type Props = {
    workers: WorkerInfo[],
    regions: RegionInfo[],
    type: WorkerTypes,
    titleName: string,
    getRegions: (skip: number, take: number) => void,
    getWorkers: (skip: number, take: number) => void,
    createWorker: (worker: CreateWorkerRequest) => void,
    editWorker: (id: number, worker: EditWorkerRequest) => void,
    createWorkerRegion: (id: number, worker: CreateWorkerRegionRequest) => void,
    editWorkerRegion: (id: number, regionId: number, worker: EditWorkerRegionRequest) => void,
    deleteWorkerRegion: (id: number, regionId: number) => void
}

class Workers extends Component<Props> {
    state: Readonly<{isRedirectingToRegions: boolean}> = {
        isRedirectingToRegions: false
    }

    componentDidMount() {
        this.props.getRegions(0, 100);
    }

    onNewRegionAdd = (): void => {
        this.setState({isRedirectingToRegions: true});
    }

    getWorkers = (skip: number, take: number) => {
        this.props.getWorkers(0, 1000);
    }

    onWorkerCreate = (item: WorkerInfoWithRegionPrices): void => {
        let workerCreated = false;

        Object.entries(item).forEach(prop => {
            if (prop[0].startsWith("rid_")) {
                const rid = Number.parseInt(prop[0].split('_')[1]);
                const newValue = prop[1] !== "NaN" ? Number.parseFloat(prop[1]) : 0;

                if (newValue !== 0 && !workerCreated) {
                    this.props.createWorker({
                        name: item.name, 
                        costPerHour: newValue, 
                        regionId: rid, 
                        type: this.props.type
                    });

                    workerCreated = true;
                } 
            }
        });
    }

    onWorkerEdit = (id: number, item: WorkerInfoWithRegionPrices): void => {
        const worker = this.props.workers.find(p => p.id === id);
        if (!worker) {
            return;
        }

        if (item.name !== worker.name) {
            this.props.editWorker(id, { type: worker.type, name: item.name });
        }

        Object.entries(item).forEach(prop => {
            if (prop[0].startsWith("rid_")) {
                const rid = Number.parseInt(prop[0].split('_')[1]);
                const newValue = prop[1] !== "NaN" ? Number.parseFloat(prop[1]) : 0;
                const oldValue = worker.workerRegions.find(p => p.regionId == rid)?.costPerHour ?? 0;

                if (oldValue === 0 && newValue !== 0) {
                    this.props.createWorkerRegion(id, { costPerHour: newValue, regionId: rid});
                } else if (newValue === 0 && oldValue !== 0) {
                    this.props.deleteWorkerRegion(id, rid);
                } else if (newValue !== oldValue) {
                    this.props.editWorkerRegion(id, rid, { costPerHour: newValue});
                }
            }
        });
    }

    onWorkerDelete = (id: number) => {
        const worker = this.props.workers.find(p => p.id === id);
        if (!worker) {
            return;
        }

        worker.workerRegions.forEach(region => {
            this.props.deleteWorkerRegion(id, region.regionId)
        });
    }

    getWorkersSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "Добавить регион",
                iconComponent: HiOutlineGlobeAlt,
                onClick: this.onNewRegionAdd
            }
        ];

        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("", 0, ValueType.Number, false, false, false));
        itemSettings.set("index", buildGridItemSettings("№ п/п", 60, ValueType.Number, false));
        itemSettings.set("type", buildGridItemSettingsWithPosibleValues("Тип", 80, ValueType.String, getWorkerTypes(), false));
        itemSettings.set("name", buildGridItemSettings("Наименование ресурса", 350, ValueType.String, true, true));

        this.props.regions.forEach(region => {
            itemSettings.set(`rid_${region.id}`, buildGridItemSettings(region.name, 150, ValueType.Money, true, false, true, 0));
        });

        return buildGridSettings(itemSettings, injectedControls);
    }

    render(): ReactNode {
        let index = 1;

        return (
            <div className={style.container}>
                <Grid 
                    items={this.props.workers.map(item => mapWorkerInfoToGridItem(item, index++, this.props.regions))}
                    settings={this.getWorkersSettings()}
                    getItems={this.getWorkers}
                    createItem={this.onWorkerCreate}
                    editItem={this.onWorkerEdit}
                    deleteItem={this.onWorkerDelete}
                />
                {
                    this.state.isRedirectingToRegions && 
                        <Redirect to={"/regions"} />
                }
            </div>
        )
    }
}

export default Workers;