import { Component, ReactNode } from "react";
import { ProjectCategories, ProjectInfo, getProjectCategories } from "./ProjectInfo";
import { RouteComponentProps } from "react-router-dom";
import style from "./Project.module.css";
import { HiCheck, HiXMark } from "react-icons/hi2";
import { RegionInfo } from "../region/RegionInfo";
import { mapToArray } from "../../utils/helpers/settings-helper";

type Props = {
    item: ProjectInfo,
    regions: Array<RegionInfo>,
    editProject: (id: number, project: ProjectInfo) => void,
    getProjectById: (id: number) => void,
    getRegions: (skip: number, take: number) => void
}

type RouteParams = {
    id: string
}

class Project extends Component<Props & RouteComponentProps<RouteParams>> {
    state: Readonly<{projectId: number, isTouched: boolean, name: string, description: string, regionId: number, category: ProjectCategories}> = {
        projectId: Number(this.props.match.params.id),
        isTouched: false,
        name: this.props.item.name,
        description: this.props.item.description,
        regionId: this.props.item.regionId,
        category: this.props.item.category
    };
    
    componentDidMount(): void {
        if (this.state.projectId) {
            this.props.getProjectById(this.state.projectId);
            this.props.getRegions(0, 100);
        }
    }

    componentDidUpdate(prevProps: Readonly<Props & RouteComponentProps<RouteParams>>): void {
        if (prevProps.item.id !== this.props.item.id) {
            this.setState({
                name: this.props.item.name,
                description: this.props.item.description,
                regionId: this.props.item.regionId,
                category: this.props.item.category
            });
        }
    }

    onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => 
        this.setState({name: event.currentTarget.value, isTouched: true});

    onDescriptionChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => 
        this.setState({description: event.currentTarget.value, isTouched: true});

    onRegionValueChange = (event: React.ChangeEvent<HTMLSelectElement>) =>
        this.setState({regionId: Number.parseInt(event.currentTarget.value), isTouched: true});

    onCategoryValueChange = (event: React.ChangeEvent<HTMLSelectElement>) =>
        this.setState({category: Number.parseInt(event.currentTarget.value), isTouched: true});

    onCancel = () => {
        this.setState({
            isTouched: false,
            name: this.props.item.name,
            description: this.props.item.description,
            regionId: this.props.item.regionId,
            category: this.props.item.category
        });
    }

    onSave = () => {
        const project: ProjectInfo = {
            id: this.props.item.id,
            name: this.state.name, 
            regionId: this.state.regionId, 
            description: this.state.description, 
            createdDate: this.props.item.createdDate,
            isSmtrSetuped: this.props.item.isSmtrSetuped,
            isStructureSetuped: this.props.item.isStructureSetuped, 
            isUpgSetuped: this.props.item.isUpgSetuped, 
            startDate: this.props.item.startDate,
            endDate: this.props.item.endDate, 
            category: this.state.category, 
            status: this.props.item.status, 
            cost: this.props.item.cost
        }

        this.props.editProject(this.props.item.id, project);
        this.setState({isTouched: false});
    }

    render(): ReactNode {
        return (            
            <div className={style.project}>
                <div className={style.controlPanel}>
                    {
                        this.state.isTouched && 
                            <HiCheck title={"Сохранить"} className={style.icon} onClick={this.onSave} />
                    }
                    {
                        this.state.isTouched && 
                            <HiXMark title={"Отменить"} className={style.icon} onClick={this.onCancel} />
                    }
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Наименование проекта"}</p>
                    </div>
                    <div className={style.data}>
                        <input type={"text"} placeholder={"Наименование проекта"} value={this.state.name} onChange={this.onNameChange} />
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Регион"}</p>
                    </div>
                    <div className={style.data}>
                        <select value={this.state.regionId} onChange={this.onRegionValueChange}>
                            {
                                this.props.regions.map(region => 
                                    <option key={region.id} value={region.id}>{region.name}</option>
                                )
                            }
                        </select>
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Информация о проекте"}</p>
                    </div>
                    <div className={style.data}>
                        <textarea className={style.textarea} placeholder={"Информация о проекте"} value={this.state.description} onChange={this.onDescriptionChange} />
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Категория"}</p>
                    </div>
                    <div className={style.data}>
                        <select value={this.state.category} onChange={this.onCategoryValueChange}>
                            {
                                mapToArray(getProjectCategories()).map(kvp => 
                                    <option key={kvp.key} value={kvp.key}>
                                        {kvp.value}
                                    </option>
                                )
                            }
                        </select>
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Дата начала"}</p>
                    </div>
                    <div className={style.data}>
                        <input type={"text"} value={this.props.item.startDate} disabled={true}/>
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Дата окончания"}</p>
                    </div>
                    <div className={style.data}>
                        <input type={"text"} value={this.props.item.endDate} disabled={true} />
                    </div>
                </div>
            </div>
        )
    }
}

export default Project;