import { Component, ReactNode } from "react";
import style from './Project.module.css';
import { CreateProjectRequest } from "./ProjectInfo";
import { RegionInfo } from "../region/RegionInfo";
import { HiPlus, HiXMark } from "react-icons/hi2";

type Props = {
    regions: Array<RegionInfo>,
    onSubmit: (data: CreateProjectRequest) => void,
    onCancel: () => void
}

class CreateNewProject extends Component<Props> {
    state: Readonly<{name: string, description: string, regionId: number}> = {
        name: "",
        description: "",
        regionId: this.props.regions[0]?.id ?? 0
    };

    onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => 
        this.setState({name: event.currentTarget.value});

    onDescriptionChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => 
        this.setState({description: event.currentTarget.value});

    onRegionValueChange = (event: React.ChangeEvent<HTMLSelectElement>) =>
        this.setState({regionId: Number.parseInt(event.currentTarget.value)});

    onSubmit = () => {
        var request : CreateProjectRequest = {
            name: this.state.name,
            description: this.state.description,
            regionId: this.state.regionId
        }

        this.props.onSubmit(request);
    }

    render(): ReactNode {
        return (
            <div className={style.project}>
                <div className={style.controlPanel}>
                    <HiPlus title={"Добавить"} className={style.icon} onClick={this.onSubmit}/>
                    <HiXMark title={"Отменить"} className={style.icon}  onClick={this.props.onCancel}/>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Наименование проекта"}</p>
                    </div>
                    <div className={style.data}>
                        <input type={"text"} autoFocus={true} placeholder={"Наименование проекта"} value={this.state.name} onChange={this.onNameChange} />
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Регион"}</p>
                    </div>
                    <div className={style.data}>
                        <select onChange={this.onRegionValueChange}>
                            {
                                this.props.regions.map(region => 
                                    <option key={region.id} value={region.id}>{region.name}</option>
                                )
                            }
                        </select>
                    </div>
                </div>
                <div className={style.group}>
                    <div className={style.header}>
                        <p>{"Информация о проекте"}</p>
                    </div>
                    <div className={style.data}>
                        <textarea placeholder={"Информация о проекте"} value={this.state.description} onChange={this.onDescriptionChange} />
                    </div>
                </div>
            </div>
        );
    }
}

export default CreateNewProject;