import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import UnitPrice from "./UnitPrice";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { editUnitPrice, getUnitPrices } from "../../store/actions/unit-price-actions";
import { getMeasurementUnits } from "../../store/actions/unit-actions";

const mapState = (store: AppStore) => {
    return ({
        items: store.unitPricesData.unitPrices,
        units: store.measurementUnitsData.measurementUnits,
        isProjectUpg: false
    });
}

export default compose<React.ComponentType>(
    connect(mapState, { getUnitPrices, editUnitPrice, getMeasurementUnits}),
    withRouter
)(UnitPrice);
