import { CSSProperties, Component, ReactNode } from "react";
import style from './Grid.module.css';
import { GridItemSettings, ValueType } from "./GridSettings";
import React from "react";
import { mapToArray } from "../../../utils/helpers/settings-helper";

type Props = {
    name: string,
    value: any,
    settings: GridItemSettings,
    isAddMode: boolean,
    isEditMode: boolean,
    isSelected: boolean,
    isFocused: boolean,
    onValueChange: (name: string, value: any) => void
}

class GridItemField extends Component<Props> {
    getDisplayStyles = (): CSSProperties => {
        let styles: CSSProperties = {};

        if (this.props.settings.alignLeft) {
            styles.textAlign = "left";
            styles.paddingLeft = 10;
            styles.width = this.props.settings.width - 10;
        } else {            
            styles.textAlign = "center"
            styles.width = this.props.settings.width;
        }

        return styles;
    }

    getStateClassName = () => {
        if (this.props.isAddMode || this.props.isEditMode) {
            return style.active;
        } else if (this.props.isSelected) {
            return style.selected;
        } else {
            return style.inactive;
        }
    }

    getTitle = (value: any): any => {
        //TODO: check if overflow, show only on "true"
        if (this.props.name.endsWith("name") || this.props.name.endsWith("Name") || this.props.name.endsWith("description")) {
            return this.getFormatedValue(value);
        }

        return "";
    }

    getFormatedValue = (value: any | undefined): any => {
        if (value === undefined || value === null || value === "") {
            return this.props.settings.defaultValueIfEmpty;
        }

        const displayValue = this.getDisplayValue(value);

        switch (this.props.settings.valueType) {
            case ValueType.Money:
                return isNaN(displayValue) ? this.props.settings.defaultValueIfEmpty : Number.parseFloat(displayValue).toFixed(2);

            case ValueType.Persents:
                return `${isNaN(displayValue) ? this.props.settings.defaultValueIfEmpty : displayValue.toFixed(2)} %`;

            default:
                return displayValue;
        }
    }

    getDisplayValue = (value: any) => {
        const key = isNaN(value) ? value : Number.parseInt(value);

        if (this.props.settings.posibleValues.size > 0) {
            return this.props.settings.posibleValues.get(key) ?? this.props.settings.defaultValueIfEmpty
        }

        return value;
    }

    onInputValueChange = (name: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
        const valueType = this.props.settings.valueType;
        const value = e.currentTarget.value;
        
        if (valueType === ValueType.Number && !Number.isNaN(value)) {
            this.props.onValueChange(name, Number.parseInt(value));
        }

        if (valueType === ValueType.Money && !Number.isNaN(value)) {
            this.props.onValueChange(name, Number.parseFloat(value).toFixed(2));
        }

        if (valueType === ValueType.String) {
            this.props.onValueChange(name, value);
        }
    }
    
    onSelectValueChange = (name: string) => (e: React.ChangeEvent<HTMLSelectElement>) => {
        this.props.onValueChange(name, e.currentTarget.value);
    }

    render(): ReactNode  {
        const isActive = this.props.settings.isEditable && (this.props.isAddMode || this.props.isEditMode);
        const isActiveInput = isActive && this.props.settings.posibleValues.size === 0;
        const isActiveSelect = isActive && this.props.settings.posibleValues.size > 0;

        return (
            <div className={style.field}>
                {
                    isActiveInput &&
                        <input
                            autoFocus={this.props.isFocused}
                            type={"text"} 
                            className={style.active}
                            style={this.getDisplayStyles()}
                            placeholder={this.props.settings.headerName}
                            value={this.props.value} 
                            onChange={this.onInputValueChange(this.props.settings.name)} 
                        />
                }
                {
                    isActiveSelect &&
                        <select autoFocus={this.props.isFocused} style={this.getDisplayStyles()} onChange={this.onSelectValueChange(this.props.settings.name)}>
                            {
                                <option style={this.getDisplayStyles()} key={this.props.value} value={this.props.value}>
                                    {this.getDisplayValue(this.props.value)}
                                </option>
                            }
                            {
                                mapToArray(this.props.settings.posibleValues).filter(kvp => kvp.key != this.props.value).map(kvp => 
                                    <option style={this.getDisplayStyles()} key={kvp.key} value={kvp.key}>
                                        {kvp.value}
                                    </option>
                                )
                            }
                        </select>
                }
                {
                    !isActive &&
                        <div title={this.getTitle(this.props.value)} className={this.getStateClassName()} style={this.getDisplayStyles()}>
                            <p>{this.getFormatedValue(this.props.value)}</p>
                        </div>
                }
            </div>
        );
    }
}

export default GridItemField;