import { Component, ReactNode } from "react";
import style from "./ProjectUpg.module.css";
import { RouteComponentProps } from "react-router-dom";
import { UnitPriceInfo } from "../../store/types/unit-price/UnitPriceInfo";
import { MeasurementUnitInfo } from "../unit/MeasurementUnitInfo";
import UnitPrice from "../unitPrice/UnitPrice";
import { ProjectInfo } from "../project/ProjectInfo";

type Props = {
    isProjectUpg: boolean,
    project: ProjectInfo,
    upg: Array<UnitPriceInfo>,
    units: Array<MeasurementUnitInfo>,
    getUnitPrices: (skip: number, take: number) => void,
    editUnitPrice: (id: number, unitPrice: UnitPriceInfo) => void,
    getMeasurementUnits: (skip: number, take: number) => void,
    getProjectById: (id: number) => void,
}

type RouteParams = {
    id: string
}

class ProjectUpg extends Component<Props & RouteComponentProps<RouteParams>> { 
    state: Readonly<{projectId: number, isUpgSetupped: boolean}> = {        
        projectId: Number(this.props.match.params.id),
        isUpgSetupped: this.props.project?.isUpgSetuped ?? false
    }

    componentDidMount(): void {
        if (this.state.projectId) {
            this.props.getProjectById(this.state.projectId);
        }
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    !this.state.isUpgSetupped &&
                        <p><strong>Setup UPG</strong></p>
                }
                {
                    this.state.isUpgSetupped &&
                        <UnitPrice 
                            isProjectUpg={this.props.isProjectUpg}
                            projectId={this.state.projectId}
                            items={this.props.upg}
                            units={this.props.units}
                            getUnitPrices={this.props.getUnitPrices}
                            editUnitPrice={this.props.editUnitPrice}
                            getMeasurementUnits={this.props.getMeasurementUnits}
                            history={this.props.history}
                            location={this.props.location}
                            match={this.props.match}
                        />
                }
            </div>
        );
    }
}

export default ProjectUpg;