import { Component, ReactNode } from "react";
import style from "./Smtr.module.css";
import { RouteComponentProps } from "react-router-dom";
import { SmtrItemInfo } from "./SmtrContainer";
import Localization from "../../utils/localization/localization";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";
import { HiListBullet } from "react-icons/hi2";

type Props = {
    getSmtr: (id: number) => void,
    items: Array<SmtrItemInfo>
}

type RouteParams = {
    id: string
}

class Smtr extends Component<Props & RouteComponentProps<RouteParams>> {   
    state: Readonly<{projectId: number, isStructureVisible: boolean}> = {
        projectId: Number(this.props.match.params.id),
        isStructureVisible: false
    }

    localization = Localization.getLocalization("smtr");

    getSmtr = (skip: number, take: number) => {
        this.props.getSmtr(this.state.projectId);
    }

    onStructureToggle = () => {
        this.setState({isStructureVisible: !this.state.isStructureVisible});
    }

    getSmtrSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "Показать/скрыть структуру ВМТР",
                iconComponent: HiListBullet,
                onClick: this.onStructureToggle
            },
        ];

        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("index", buildGridItemSettings("№ п/п", 60, ValueType.Number, false));
        itemSettings.set("stage", buildGridItemSettings("Стадия", 70, ValueType.String));
        itemSettings.set("title", buildGridItemSettings("Титул", 70, ValueType.Number));
        itemSettings.set("titleName", buildGridItemSettings("Наименование титула", 300, ValueType.String));
        itemSettings.set("brand", buildGridItemSettings("Марка / Том", 70, ValueType.String));
        itemSettings.set("techSpecCode", buildGridItemSettings("Шифр тех. спецификации", 170, ValueType.String));
        itemSettings.set("revision", buildGridItemSettings("Рев. тех. док.", 80, ValueType.Number));
        itemSettings.set("constructive", buildGridItemSettings("Конструктив", 200, ValueType.String));
        itemSettings.set("position", buildGridItemSettings("Позиция", 80, ValueType.Number));
        itemSettings.set("techName", buildGridItemSettings("Наименование и техническая характеристика", 450, ValueType.String, true, true));
        itemSettings.set("docType", buildGridItemSettings("Тип, марка", 120, ValueType.String));
        itemSettings.set("code", buildGridItemSettings("Код продукции", 120, ValueType.String));
        itemSettings.set("manufacturer", buildGridItemSettings("Поставщик", 100, ValueType.String));
        itemSettings.set("units", buildGridItemSettings("Ед. изм.", 70, ValueType.String));
        itemSettings.set("quantityInProject", buildGridItemSettings("Кол-во по проекту", 90, ValueType.Money));
        itemSettings.set("mass", buildGridItemSettings("Масса 1 ед., кг", 80, ValueType.Number));
        itemSettings.set("note", buildGridItemSettings("Примечания", 200, ValueType.String));
        itemSettings.set("consumptionRate", buildGridItemSettings("Норма расхода", 100, ValueType.Persents));
        itemSettings.set("consumptionQuantity", buildGridItemSettings("Кол-во с учетом нормы расхода", 150, ValueType.Money, false));
        itemSettings.set("resourceType", buildGridItemSettings("Классификатор", 150, ValueType.String));
        itemSettings.set("delivery", buildGridItemSettings("Разделение поставки", 120, ValueType.String));
        // itemSettings.set("pricePerUnit", buildGridItemSettings("", 110, ValueType.Number));
        // itemSettings.set("cost", buildGridItemSettings("", 110, ValueType.Number));
        itemSettings.set("workCode", buildGridItemSettings("Код работ", 150, ValueType.String));
        itemSettings.set("workName", buildGridItemSettings("Наименование работ", 600, ValueType.String, true, true));
        itemSettings.set("workUnits", buildGridItemSettings("Ед. изм.", 70, ValueType.String));
        itemSettings.set("coeficient", buildGridItemSettings("K", 60, ValueType.Money));
        itemSettings.set("quantity", buildGridItemSettings("Количество", 110, ValueType.Money, false));

        return buildGridSettings(itemSettings, injectedControls);
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    this.state.isStructureVisible && 
                        <div className={style.structureContainer}>
                            <p><strong>Структура ВМТР</strong></p>
                        </div>
                }
                <div className={style.gridContainer}>
                    <Grid
                        items={this.props.items}
                        settings={this.getSmtrSettings()}
                        getItems={this.getSmtr}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
            </div>
        );
    }
}

export default Smtr;