import Header from "./Header";
import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import { restoreModules } from "../../store/actions/menu-actions";

const mapState = (store: AppStore) => {
    return ({
        menuItems: store.menuData.menuItems
    });
};

export default connect(mapState, {restoreModules})(Header);
