import { CreateWorkerRegionRequest, CreateWorkerRequest, EditWorkerRegionRequest, EditWorkerRequest, WorkerInfo } from "../components/worker/WorkerInfo";
import Api from "./api-service";

const WORKER_SERVICE_ROUTE = "Worker";

const workerService = {
    getWorkers(skip: number, take: number): Promise<Array<WorkerInfo>> {
        return Api.get<Array<WorkerInfo>>(`${WORKER_SERVICE_ROUTE}?skip=${skip}&take=${take}`).then(res => res.data);
    },

    addWorker(worker: CreateWorkerRequest): Promise<WorkerInfo> {
        return Api.post<WorkerInfo>(`${WORKER_SERVICE_ROUTE}`, worker).then(res => res.data);
    },

    editWorker(id: number, worker: EditWorkerRequest): Promise<WorkerInfo> {
        return Api.put<WorkerInfo>(`${WORKER_SERVICE_ROUTE}/${id}`, worker).then(res => res.data);
    },

    addWorkerRegion(id: number, workerRegion: CreateWorkerRegionRequest): Promise<WorkerInfo> {
        return Api.post<WorkerInfo>(`${WORKER_SERVICE_ROUTE}/${id}/region`, workerRegion).then(res => res.data);
    },

    editWorkerRegion(id: number, regionId: number, workerRegion: EditWorkerRegionRequest): Promise<WorkerInfo> {
        return Api.put<WorkerInfo>(`${WORKER_SERVICE_ROUTE}/${id}/region/${regionId}`, workerRegion).then(res => res.data);
    },

    deleteWorkerRegion(id: number, regionId: number): Promise<WorkerInfo> {
        return Api.delete<WorkerInfo>(`${WORKER_SERVICE_ROUTE}/${id}/region/${regionId}`).then(res => res.data);
    }
}

export default workerService;