import { Component } from "react";
import style from './Module.module.css';
import { NavLink } from "react-router-dom";
import Localization from "../../utils/localization/localization";

type Props = {
    name: string,
    link: string,
    isActive: boolean
}

class Module extends Component<Props> {
    private localization = Localization.getLocalization("menu");

    getClasses = () => {
        if (!this.props.name) {
            return style.emptyModule;
        }

        if (!this.props.isActive) {
            return style.module;
        }

        return `${style.module} ${style.active}`;
    }

    render() {
        return (            
            <NavLink className={this.getClasses()} to={this.props.link}>{this.localization(this.props.name)}</NavLink>
        );
    }
}

export default Module;
