import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import ProjectUpg from "./ProjectUpg";
import { editUnitPrice, getUnitPrices } from "../../store/actions/unit-price-actions";
import { getMeasurementUnits } from "../../store/actions/unit-actions";
import { getProjectById } from "../../store/actions/project-actions";

const mapState = (store: AppStore) => {
    return ({
        project: store.projectsData.activeProject,
        upg: store.unitPricesData.unitPrices,
        units: store.measurementUnitsData.measurementUnits,
        isProjectUpg: true
    });
}

export default compose<React.ComponentType>(
    connect(mapState, { getUnitPrices, editUnitPrice, getMeasurementUnits, getProjectById /* getUpgList,  */}),
    withRouter
)(ProjectUpg);