import { AppActions } from "../redusers/redux-store";
import { Dispatch } from "redux";
import { ModuleInfo } from "../types/menu/ModuleInfo";
import { SET_ACTIVE_ITEM_ACTION, SET_MENU_ITEMS_ACTION } from "../redusers/menu-redusers";

const menu: { [name: string]: Array<ModuleInfo> } = {
    project:  [
        {name: "project", link: `/projects/$id`},
        {name: "workingGroup", link: `/projects/$id/workingGroup`},
        {name: "structure", link: `/projects/$id/structure`},
        {name: "summary", link: `/projects/$id/summary`},
        {name: "directExpenses", link: `/projects/$id/directExpenses`},
        {name: "indirectExpenses", link: `/projects/$id/indirectExpenses`},
        {name: "smtr", link: `/projects/$id/smtr`},
        {name: "projectUPG", link: `/projects/$id/projectUPG`},
        {name: "", link: `/projects/$id/projectUPG/$subId`},
        {name: "projectWorkers", link: `/projects/$id/projectWorkers`},
        {name: "projectMachines", link: `/projects/$id/projectMachines`},
        // {name: "diagram", link: `/projects/$id/diagram`},
    ],
    projects: [
        {name: "active", link: `/projects/active`},
        {name: "finished", link: `/projects/finished`},
        {name: "paused", link: `/projects/paused`}
    ],
    upg: [
        {name: "unitPriceGuide", link: "/upg"},
        {name: "machines", link: "/machines"},
        {name: "workers", link: "/workers"}
    ],
    admin: [
        {name: "regions", link: "/regions"},
        {name: "measurementUnits", link: "/units"}
    ],

}

const actions = {
    setItems: (items: Array<ModuleInfo>) => ({type: SET_MENU_ITEMS_ACTION, items} as const),
    setActiveItem: (activeItem: string) => ({type: SET_ACTIVE_ITEM_ACTION, activeItem} as const)
}

export const restoreModules = (currentLink: string) => (dispatch: Dispatch) => {
    const id = currentLink.split('/').find(p => Number.parseInt(p));
    let link = id ? currentLink.replace(id, '$id') : currentLink;

    const subId = link.split('/').find(p => Number.parseInt(p));
    link = subId ? link.replace(subId, '$subId') : link;

    const moduleName = currentLink.substring(1).split('/')[0];

    for (const [name, modules] of Object.entries(menu)) {
        if (name === moduleName || modules.some(module => module.link === link)) {
            const data = menu[name].map<ModuleInfo>(module => ({
                name: module.name,
                link: id ? module.link.replace('$id', id) : module.link
            }));

            dispatch(actions.setItems(data));
            dispatch(actions.setActiveItem(name));
            return;
        }
    }

    if (!menu[moduleName]) {
        dispatch(actions.setActiveItem(moduleName));
        dispatch(actions.setItems([]));
    }
}

export type MenuActions = AppActions<typeof actions>