import { Component, ReactNode } from "react";
import style from './MeasurementUnit.module.css';
import { MeasurementUnitInfo } from "./MeasurementUnitInfo";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    items: Array<MeasurementUnitInfo>,
    getMeasurementUnits: (skip: number, take: number) => void,
    createMeasurementUnit: (item: MeasurementUnitInfo) => void,
    editMeasurementUnit: (id: number, item: MeasurementUnitInfo) => void,
    deleteMeasurementUnit: (id: number) => void
}

class MeasurementUnit extends Component<Props> {
    getSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 100, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Расшифровка", 350, ValueType.String, true, true));
        itemSettings.set("code", buildGridItemSettings("Ед.изм", 150, ValueType.String));

        return buildGridSettings(itemSettings);
    }

    render(): ReactNode  {
        return (
            <div className={style.container}>
                <Grid
                    items={this.props.items}
                    settings={this.getSettings()}
                    getItems={this.props.getMeasurementUnits}
                    createItem={this.props.createMeasurementUnit}
                    editItem={this.props.editMeasurementUnit}
                    deleteItem={this.props.deleteMeasurementUnit}
                />
            </div>
        );
    }
}

export default MeasurementUnit;