import { Component, ReactNode } from "react";
import style from "./Structure.module.css";
import { RouteComponentProps } from "react-router-dom";
import { StructureItemInfo } from "./StructureItemInfo";
import Localization from "../../utils/localization/localization";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";

type Props = {
    getStructure: (id: number) => void,
    items: Array<StructureItemInfo>
}

type RouteParams = {
    id: string
}

class Structure extends Component<Props & RouteComponentProps<RouteParams>> {
    state: Readonly<{projectId: number}> = {
        projectId: Number(this.props.match.params.id)
    }

    localization = Localization.getLocalization("structure");

    getStructure = (skip: number, take: number) => {
        this.props.getStructure(this.state.projectId);
    }

    getProjectStructureSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("title", buildGridItemSettings(this.localization("title"), 70, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings(this.localization("name"), 300, ValueType.String, false));
        itemSettings.set("mark", buildGridItemSettings(this.localization("mark"), 50, ValueType.String, false));
        itemSettings.set("code", buildGridItemSettings(this.localization("code"), 170, ValueType.String, false));
        itemSettings.set("type", buildGridItemSettings(this.localization("type"), 120, ValueType.String, false));
        itemSettings.set("date", buildGridItemSettings(this.localization("date"), 150, ValueType.String, false));

        return buildGridSettings(itemSettings, [], false, false, false, true, true);
    }

    render(): ReactNode {
        return (
            <div className={style.structureContainer}>
                <Grid
                    items={this.props.items}
                    settings={this.getProjectStructureSettings()}
                    getItems={this.getStructure}
                    createItem={() => {}}
                    editItem={() => {}}
                    deleteItem={() => {}}
                />
            </div>
        );
    }
}

export default Structure;