import { Component, useEffect, useState } from "react";
import style from './Header.module.css';
import Module from "../module/Module";
import { ModuleInfo } from "../../store/types/menu/ModuleInfo";
import { useLocation } from "react-router-dom";

type Props = {
    menuItems: Array<ModuleInfo>,
    restoreModules: (currentLink: string) => void
}

function Header(props: Props) {
    let nextLocation = useLocation();
    let [location, setLocation] = useState<string>();

    useEffect(() => {
        if (location !== nextLocation.pathname) {
            setLocation(nextLocation.pathname);
            props.restoreModules(nextLocation.pathname);
        }
    }, [nextLocation])
    
    return (
        <header className={style.header}>
            {
                props.menuItems.map(item => <Module 
                    key={item.name} 
                    isActive={item.link === location} 
                    link={item.link} 
                    name={item.name} 
                />)
            }
        </header>
    );
}

export default Header;
