import { IGridable } from "../common/grid/GridSettings"
import { RegionInfo } from "../region/RegionInfo"

export interface CreateWorkerRequest {
    name: string,
    type: WorkerTypes,
    costPerHour: number,
    regionId: number
}

export interface EditWorkerRequest {
    name: string,
    type: WorkerTypes
}

export interface CreateWorkerRegionRequest {
    costPerHour: number,
    regionId: number
}

export interface EditWorkerRegionRequest {
    costPerHour: number
}

export interface WorkerInfo {
    id: number,
    name: string,
    type: WorkerTypes,
    workerRegions: WorkerRegionInfo[]
}

export interface WorkerRegionInfo {
    costPerHour: number,
    regionId: number
}

export enum WorkerTypes {
    Worker,
    Machine
}

export const getWorkerTypes = (): Map<any, any> => {
    const map = new Map<any, any>();
    map.set(0, "ОР");
    map.set(1, "МиМ");
    return map;
}

export interface WorkerInfoWithRegionPrices extends IGridable {    
    id: number,
    index: number,
    name: string,
    type: WorkerTypes
}

export const mapWorkerInfoToGridItem = (info: WorkerInfo, index: number, regions: RegionInfo[]): WorkerInfoWithRegionPrices => {
    let item: WorkerInfoWithRegionPrices = {
        id: info.id,
        index: index,
        name: info.name,
        type: info.type
    };

    regions.forEach(region => {
        const existingRegion = info.workerRegions.find(p => p.regionId === region.id);
        item[`rid_${region.id}`] = existingRegion?.costPerHour ?? 0;
    });

    return item;
}