import { Component, ReactNode } from "react";
import style from './Project.module.css';
import { BaseProjectInfo, CreateProjectRequest, ProjectCategories, ProjectCostInfo, ProjectPeriodInfo, ProjectStatusInfo, getProjectStatuses, mapProjectToBaseProject, 
    mapProjectToProjectCost, mapProjectToProjectPeriod, mapProjectToProjectStatus } from "./ProjectInfo";
import { ProjectInfo } from "./ProjectInfo";
import { HiPlus, HiOutlineArrowLeftOnRectangle } from "react-icons/hi2";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { RegionInfo } from "../region/RegionInfo";
import { ArrayToMap, buildGridItemSettings, buildGridItemSettingsWithPosibleValues, buildGridSettingsWithCrossTable } from "../../utils/helpers/settings-helper";
import CreateNewProject from "./CreateNewProject";
import { Redirect } from "react-router-dom";

type Props = {
    categories: ProjectCategories,
    items: Array<ProjectInfo>,
    regions: Array<RegionInfo>,
    createProject: (project: CreateProjectRequest) => void,
    getProjects: (skip: number, take: number) => void,
    editProject: (id: number, project: ProjectInfo) => void,
    deleteProject: (id: number) => void,
    getRegions: (skip: number, take: number) => void
}

class Projects extends Component<Props> {
    state: Readonly<{isAddingNewProject: boolean, isRedirectingToProject: boolean, selectedId: number}> = {
        isAddingNewProject: false,
        isRedirectingToProject: false,
        selectedId: 0,
    }

    componentDidMount(): void {
        this.props.getRegions(0, 100);
    }

    onItemSelected = (id: number): void => {
        this.setState({selectedId: id});
    }

    onProjectEntering = (): void => {
        if (this.state.selectedId) {
            this.setState({isRedirectingToProject: true});
        }
    }

    onNewProjectCreating = (): void => {
        this.setState({
            isAddingNewProject: true
        });
    }

    onNewProjectSubmit = (data: CreateProjectRequest): void => {
        this.props.createProject(data);        
        this.setState({isAddingNewProject: false});
    }

    onNewProjectCancel = (): void => {
        this.setState({isAddingNewProject: false});
    }

    onBaseProjectInfoEdit = (id: number, item: BaseProjectInfo): void => {
        console.log(item);
    }

    onProjectPeriodInfoEdit = (id: number, item: ProjectPeriodInfo): void => {
        console.log(item);
    }

    onProjectStatusInfoEdit = (id: number, item: ProjectStatusInfo): void => {
        console.log(item);
    }

    onProjectCostInfoEdit = (id: number, item: ProjectCostInfo): void => {
        console.log(item);
    }

    getBaseProjectInfoSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "Новый проект",
                iconComponent: HiPlus,
                onClick: this.onNewProjectCreating
            },            
            {
                title: "Перейти к проекту",
                iconComponent: HiOutlineArrowLeftOnRectangle,
                onClick: this.onProjectEntering
            }
        ];

        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 70, ValueType.Number, false));
        itemSettings.set("name", buildGridItemSettings("Наименование проекта", 450, ValueType.String, true, true));
        itemSettings.set("regionId", buildGridItemSettingsWithPosibleValues("Регион", 200, ValueType.Number, ArrayToMap(this.props.regions, p => p.id, p => p.name)));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, injectedControls, false, true, false, true, true);
    }

    getProjectPeriodInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 100, ValueType.Number, false, false, false));
        itemSettings.set("period", buildGridItemSettings("Период реализации", 200, ValueType.String, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    getProjectStatusInfoSettings = (): GridSettings => {
        const statuses = getProjectStatuses();

        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 100, ValueType.Number, false, false, false));
        itemSettings.set("status", buildGridItemSettingsWithPosibleValues("Статус", 180, ValueType.String, statuses, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }
    
    getProjectCostInfoSettings = (): GridSettings => {
        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("id", buildGridItemSettings("№пп", 100, ValueType.Number, false, false, false));
        itemSettings.set("cost", buildGridItemSettings("Стоимость", 120, ValueType.Money, false));

        return buildGridSettingsWithCrossTable(itemSettings, this.state.selectedId, this.onItemSelected, [], false, false, false, true, false);
    }

    render(): ReactNode {
        const projects = this.props.items.filter(p => this.props.categories & p.category);

        return (
            <div className={style.projectsContainer}>
                {
                    !this.state.isAddingNewProject && this.props.regions.length > 0 && 
                        <div className={style.gridContainer}>
                            <Grid
                                items={projects.map(item => mapProjectToBaseProject(item, ArrayToMap(this.props.regions, p => p.id, p => p.name)))}
                                settings={this.getBaseProjectInfoSettings()}
                                getItems={this.props.getProjects}
                                createItem={() => {}}
                                editItem={this.onBaseProjectInfoEdit}
                                deleteItem={this.props.deleteProject}
                            />
                            <Grid
                                items={projects.map(item => mapProjectToProjectPeriod(item))}
                                settings={this.getProjectPeriodInfoSettings()}
                                getItems={() => {}}
                                createItem={() => {}}
                                editItem={this.onProjectPeriodInfoEdit}
                                deleteItem={() => {}}
                            />
                            <Grid
                                items={projects.map(item => mapProjectToProjectStatus(item))}
                                settings={this.getProjectStatusInfoSettings()}
                                getItems={() => {}}
                                createItem={() => {}}
                                editItem={this.onProjectStatusInfoEdit}
                                deleteItem={() => {}}
                            />
                            <Grid
                                items={projects.map(item => mapProjectToProjectCost(item))}
                                settings={this.getProjectCostInfoSettings()}
                                getItems={() => {}}
                                createItem={() => {}}
                                editItem={this.onProjectCostInfoEdit}
                                deleteItem={() => {}}
                            />
                        </div>
                }
                {
                    this.state.isAddingNewProject && 
                        <CreateNewProject 
                            regions={this.props.regions}
                            onSubmit={this.onNewProjectSubmit} 
                            onCancel={this.onNewProjectCancel}
                        />
                }
                {
                    this.state.isRedirectingToProject && this.state.selectedId && 
                        <Redirect to={`/projects/${this.state.selectedId}`} />
                }
            </div>
        )
    }
}

export default Projects;