import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import HeaderContainer from './components/header/HeaderContainer';
import MachineContainer from './components/machine/MachineContainer';
import WorkersContainer from './components/worker/WorkersContainer';
import ProjectsContainer from './components/project/ProjectsContainer';
// import UnitPriceContainer from './components/oldUnitPrice/UnitPriceContainer';
// import UnitPriceCalculationContainer from './components/oldUnitPrice/UnitPriceCalculationContainer';
// import GuideContainer from './components/guide/GuideContainer';
import RegionsContainer from './components/region/RegionsContainer';
import DiagramContainer from './components/diagram/DiagramContainer';
import MeasurementUnitContainer from './components/unit/MeasurementUnitContainer';
import TaskContainer from './components/task/TaskContainer';
import MainMenuContainer from './components/mainMenu/MainMenuContainer';
import ProjectContainer from './components/project/ProjectContainer';
import WorkingGroupContainer from './components/workingGroup/WorkingGroupContainer';
import SmtrContainer from './components/smtr/SmtrContainer';
import StructureContainer from './components/structure/StructureContainer';
import WorContainer from './components/wor/WorContainer';
// import initData from './store/actions/init-actions';
import SummaryContainer from './components/summary/SummaryContainer';
import IndirectExpensesContainer from './components/indirectExpenses/IndirectExpensesContainer';
import ProjectMachinesContainer from './components/projectMachines/ProjectMachinesContainer';
import ProjectWorkersContainer from './components/projectWorkers/ProjectWorkersContainer';
import ProjectUpgContainer from './components/projectUpg/ProjectUpgContainer';
import Admin from './components/admin/Admin';
import UpgContainer from './components/unitPrice/UpgContainer';
import UnitPriceContainer from './components/unitPrice/UnitPriceContainer';
import { ProjectCategories } from './components/project/ProjectInfo';
import Settings from './components/settings/Settings';
import { activateTheme } from './utils/helpers/theme-helper';
import UnitPriceCalculationContainer from './components/unitPrice/UnitPriceCalculationContainer';
import ProjectUpgCalculationContainer from './components/projectUpg/ProjectUpgCalculationContainer';

class App extends Component {
  componentDidMount(): void {
    activateTheme();
    // initData();
  }

  render(): React.ReactNode {
    return (
      <div className='app-wrapper'>
        <HeaderContainer />
        <TaskContainer />
        <div className='app-wrapper-content'>
          <Switch>
            <Route path='/machines' render={() => <MachineContainer />} />
            <Route path='/workers' render={() => <WorkersContainer/>} />
            <Route path='/regions' render={() => <RegionsContainer />} />
            <Route path='/units' render={() => <MeasurementUnitContainer />} />
            <Route path='/admin' render={() => <Admin />} />
            <Route path='/settings' render={() => <Settings />} />

            <Route path='/upg' exact={true} render={() => <UpgContainer />} />
            <Route path='/upg/:id' exact={true} render={() => <UnitPriceContainer />} />
            <Route path='/upg/:id/:upgId' render={() => <UnitPriceCalculationContainer />} />

            <Route path='/projects' exact={true} render={() => <ProjectsContainer categories={ProjectCategories.All} />} />
            <Route path='/projects/active' exact={true} render={() => <ProjectsContainer categories={ProjectCategories.Current} />} />
            <Route path='/projects/finished' exact={true} render={() => <ProjectsContainer categories={ProjectCategories.Finished} />} />
            <Route path='/projects/paused' exact={true} render={() => <ProjectsContainer categories={ProjectCategories.Paused} />} />
            <Route path='/projects/:id' exact={true} render={() => <ProjectContainer />} />
            <Route path='/projects/:id/diagram' render={() => <DiagramContainer/>} />
            <Route path='/projects/:id/workingGroup' render={() => <WorkingGroupContainer/>} />
            <Route path='/projects/:id/smtr' render={() => <SmtrContainer/>} />
            <Route path='/projects/:id/structure' render={() => <StructureContainer/>} />
            <Route path='/projects/:id/summary' render={() => <SummaryContainer/>} />
            <Route path='/projects/:id/directExpenses' render={() => <WorContainer/>} />
            <Route path='/projects/:id/indirectExpenses' render={() => <IndirectExpensesContainer />} />
            <Route path='/projects/:id/projectMachines' render={() => <ProjectMachinesContainer/>} />
            <Route path='/projects/:id/projectWorkers' render={() => <ProjectWorkersContainer/>} />
            <Route path='/projects/:id/projectUpg' exact={true} render={() => <ProjectUpgContainer/>} />
            <Route path='/projects/:id/projectUpg/:upgId' render={() => <ProjectUpgCalculationContainer/>} />
          </Switch>
        </div>
        
        <MainMenuContainer />
      </div>
    )
  }  
}

export default App;
