import { IGridable } from "../common/grid/GridSettings";

export interface WorItemInfo extends IGridable{
    baseCode: string,
    subject: string,
    code: string,
    name: string,
    units: string,
    volume : number,
    pricePerUnit: number,
    price: number
}

export interface WorBaseInfo {
    index: number,
    baseCode: string,
    subject: string,
    code: string,
    name: string,
    units: string,
    volume : number
}

export interface WorPriceInfo {
    workerLaborPerUnit: number,
    machineLaborPerUnit: number,
    workerCostsPerHour: number,
    workerCostsPerUnit: number,
    machineCostsPerHour: number,
    machineCostsPerUnit: number,
    dirrectCostsPerUnit: number,
}

export interface WorPriceTotal {
    workerLabor: number,
    machineLabor: number,
    workerCosts: number,
    machineCosts: number,
    dirrectCosts: number
}

export const mapWorItemInfoToWorBaseInfo = (item: WorItemInfo, index: number): WorBaseInfo => ({
    index: index,
    baseCode: item.baseCode,
    subject: item.subject,
    code: item.code,
    name: item.name,
    units: item.units,
    volume : item.volume
});

export const mapWorItemInfoToWorPriceInfo = (item: WorItemInfo): WorPriceInfo => ({
    workerLaborPerUnit: 312.43,
    machineLaborPerUnit: 312.43,
    workerCostsPerHour: 312.43,
    workerCostsPerUnit: 312.43,
    machineCostsPerHour: 312.43,
    machineCostsPerUnit: 312.43,
    dirrectCostsPerUnit: 312.43,
});

export const mapWorItemInfoToWorPriceTotal = (item: WorItemInfo): WorPriceTotal => ({
    workerLabor: 312.43,
    machineLabor: 312.43,
    workerCosts: 312.43,
    machineCosts: 312.43,
    dirrectCosts: 312.43
});