export interface IndirectExpensesInfo {
    id: number,
    index: number,
    name: string,
    currency: string,
    costs: number
}

export const mapIndirectExpensesToGridModel = (item: IndirectExpensesInfo, index: number): IndirectExpensesInfo => ({
    id: item.id,
    index: index,
    name: item.name,
    currency: item.currency,
    costs: item.costs
})